<template>
  <div class="product">
   <Title titleName='商品详情' iconLeft="back"/>
	<div>
<van-swipe :autoplay="3000" class="my-swipe" height="auto" indicator-color="white" :height="250">
		  <van-swipe-item v-for="(image, index) in news.images" :key="index">
		    <img :src="image" />
		  </van-swipe-item>
		</van-swipe>
		</div>
		<div class="pro-fund">
			<h1>￥{{news.price_sale}}</h1>
			<span>￥{{news.price_market}}</span>
		</div>
<div class="pro-l1">
	<div class="sn1">
		<h3>{{news.title}}</h3>
		<div class="sl">生产厂家：{{news.factory}}</div>
		<h4>主治功能</h4>
		<div class="sl">{{news.remark}}</div>
		<div class="sr">
			注：凭借支付凭证，到店刷医保卡支付可返现
		</div>
	</div>

</div>
<div class="pro-l2">
	<div class="can" @click="lang=true">
		<div class="ed">规格</div>
		<div class="pl">{{news.key_vals}}<img src="../../assets/ip.png"></div>
	</div>
	<!-- <div class="can1">
		<div>產品詳情</div>
	
	</div>
	<div v-html="news.content"></div> -->
</div>
<div class="pro-l3">
	<div class="fiv-ind">
		<ul>
			<li :class="{'han':active==1}" @click="active=1">详情</li>
			<li :class="{'han':active==2}" @click="active=2">评价</li>
		</ul>
	</div>
	<div class="fiv-con">
		<div v-if="active==1" v-html="news.content"></div>
		<div class="com-full" v-if="active==2">
			<ul>
				<li v-for="item in pull">
					<div class="com-l1">
						<div class="pt">
						<div class="hed">
							<img v-if="item.touxiang==''" src="../../assets/head.png" />
							<img v-else :src="item.touxiang" />
						</div>
						<div class="nam">
							{{item.username}}
						</div>
						</div>
						
						<van-rate
						  class="rate"
						  :value="item.star"
						  allow-half
						  :size="12"
						  color="#F5A628"
						  void-icon="star"
						  void-color="#eee"
						/>
					</div>
					<div class="com-l2">
						{{item.content}}
					</div>
					<div class="com-l4">
						规格：{{item.key_vals}}
					</div>
				</li>
			
			</ul>
		</div>
	</div>
</div>
	<div class="adr">
		<div class="lt">
			<ul>
				<li>
					<router-link to="/">
					<img src="../../assets/detail_poster.png" />
					<p>首页</p>
					</router-link>
				</li>
				<li>
					<router-link to="/index/contact">
					<img src="../../assets/detail_customer.png" />
					<p>客服</p>
					</router-link>
				</li>
				<li>
					<router-link to="/user/cart">
					<img src="../../assets/detail_car.png" />
					<p>购物车</p>
					</router-link>
				</li>
			</ul>
		</div>
		<div class="rt">
			<div class="h-button" @click="sandYart">加入购物车</div>
			<div class="h-button but2" @click="to_add">立即购买</div>
		</div>
	</div>
<van-popup v-model="lang" position="bottom" class="language" closeable>
		<div class="cont">
			<van-row gutter="20">
			  <van-col span="8">
				  <img class="tim" :src="news.images[0]" />
			  </van-col>
			  <van-col span="14">
				  <h3>{{news.title}}</h3>
				  <p class="ba">￥{{news.price_sale}}</p>
				  <p class="bla">￥{{news.price_sale}}</p>
			  </van-col>
			
			</van-row>
			<div class="pro-nlt2" v-if="news.inventorys.length>0">
				<div>规格</div>
				<ul>
					<li :class="{'gate':item.is_selected}" @click="invent(item)" v-for="item in news.inventorys">{{item.key_vals}}</li>
				
				</ul>
			</div>
			<div class="pro-nlt">
				<div>数量</div>
				<van-stepper v-model="num" />
			</div>
			
			<van-row gutter="10">
			  <van-col span="12">
				 <van-button color="#F29F25" @click="sandYart" block>加入购物车</van-button>
			  </van-col>
			  <van-col span="12">
				 <van-button color="#FF5E00"  @click="to_add" block>立即购买</van-button>
			  </van-col>
			
			</van-row>
		</div>
		</van-popup>

	
  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import { Toast } from 'vant';
import { Dialog } from 'vant';
export default {
  components: {
    Title,
    CardList
  },
  data() {
    return {
		active: 1,
		id:'',
		dialogVisible: false,
		ride:0,
		num: 1,
		info:'',
		news:[],
		notice:[],
		pull:[],
		total:'',
		cart:false,
		roll:'',
		price:0,
		ticket:'',
		pro:'',
		lang:false
		
		    

    };
  },  mounted() {
 this.id = this.$route.query.id;
this.sandMate();
this.sandMates()

		},methods: {
			more(){
				this.page+=1;
				this.sandMate()
			},
		
		sandMate(){
			this.$axios({
			  method: "post",
			  url: "service.php?action=product_detail",
			  data: {inventoryId:this.id},  
			}).then((res) => {
				let data=res.data;
				
				if(data.code==1){
					let arr=[]
					this.news=data.data;
					
					let content = this.news.content
					const regex = new RegExp('<img', 'gi')
					this.news.content = content.replace(regex, `<img style="max-width: 100%; height: auto"`)
	
					if(this.news.ticket_info.had_ticket==0){
						
					
							this.ticket=this.news.ticket_info
						Dialog.confirm({
						  title: '積分兌換購買券，可優先購買。',
						  message: '或於北京时間'+this.ticket.startTime+' 購買',
						})
						  .then(() => {
							this.voucher()
						  });
						
					
						
						
					}
					
					this.sandCase(this.news.channelId)
				}
			 
			});
		
		},	sandYart(){
				if(window.localStorage.getItem('user')== "undefined"||!window.localStorage.hasOwnProperty('user')){
					  this.$router.push({ path: "/logins" });	
					return
				}
			
				this.$axios({
				  method: "post",
				  url: "service.php?action=gouwuche_add",
				  data: {inventoryId:this.news.inventoryId,num:this.num},  
				}).then((res) => {
					let data=res.data;
					
					if(data.code==1){
						Toast(data.message);
						
					}else{
						Toast(data.message);
					}
				 
				});
			},	voucher(){
			
							this.$axios({
							  method: "post",
							  url: "service.php?action=money_createTicket",
							  data: {ticket_id:this.ticket.id},  
							}).then((res) => {
								let data=res.data;
								
								if(data.code==1){
								Toast(data.message);
									
									this.sandMate();
								
								}else{
								Toast(data.message);
								
								}
							 
							});
						},to_add() {
				let product_info = [{
					"inventoryId": this.id,
					"productId": this.news.productId,
					"num": this.num
				}]
			
				let arr = [{
					image: this.news.images[0],
					title: this.news.title,
					price_sale: this.news.price_sale,
					key_vals: this.news.key_vals,
					num: this.num
				}]
	
			 localStorage.setItem("order_info", JSON.stringify(arr));
		
			 this.$router.push({ path: "/user/order?product_info="+ JSON.stringify(product_info) });	
			},collect(it){
			
							if(window.localStorage.getItem('user')== "undefined"){
								  this.$router.push({ path: "/logins" });	
								return
							}
							this.$axios({
							  method: "post",
							  url: "service.php?action=product_collect",
							  data: {inventoryId:this.id,collect_type:it},  
							}).then((res) => {
								let data=res.data;
								
								if(data.code==1){
								
									this.sandMate();
								
								}
							 
							});
						},sandMates(){
			 	this.$axios({
			 	  method: "post",
			 	  url: "service.php?action=product_comments",
			 	  data: {productId:this.id},  
			 	}).then((res) => {
			 		let data=res.data;
			
			 		
			 	
			 				 this.pull=data.data;			
			 			
			 			this.total=data.count
			 			
			 		
			 	 
			 	});
			 
			 },sandAlt(){
				this.news=[]
				this.sandMate()
			
			},invent(item){
				this.id=item.inventoryId
				this.sandMate();
			},sandHans(){
			
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_channel",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
			
							data.data.forEach((item, index) => {
								if(this.channel_id==item.id){
									this.title=item.title
									this.hans=item.channels
									if(this.hans.length>0){
										this.channel_id=this.hans[0].id
									}
									this.sandMate();
									return
								}
							})			
					
						
					}
				 
				});
			
			}
		}
};
</script>
<style lang="scss" scoped>

	.my-swipe .van-swipe-item {
