			}
			.han{
				color: #FF5E00;
				border-bottom:4px solid #FF5E00 ;
				
				
			}
		}
		 
	
	}
	.fiv-con{
		background: #ffffff;
		
	}
	.com-full{
		padding: 20px;
		li{
			margin-bottom: 20px;
		}
		.com-l1{
			display: flex;
			justify-content: space-between;
			.pt{
				display: flex;
				align-items: center;
			}
			.hed{
				width: 48px;
				height: 48px;
				background: #FFFFFF;
				border-radius: 50%;
				margin-right: 10px;
				img{
					width: 100%;
				}

			}
		}
		.com-l2{
			margin: 20px 0;
		}
		.com-l4{
		
			font-size: 24px;
			font-family: PingFang SC;
			font-weight: 400;
			color: #BDBDBD;
			

		}
	}
}
.language{

	.cont{
		margin: 20px;
		.tim{
			width: 100%;
		}
		h3{
			margin-bottom: 10px;
		}
		.ba{
			font-size: 30px;
			color: #E42626;
			margin-top: 30px;
		}
		.bla{
			font-size: 24px;
			color: #BDBDBD;
			text-decoration: line-through;
		}
		.pro-nlt{
			display: flex;
			justify-content: space-between;
			margin: 20px 0;
		}
		.pro-nlt2{
			ul{
				display: flex;
				flex-wrap: wrap;
				li{
					padding: 10px 20px;
					cursor: pointer;
					height: 30px;
					line-height: 30px;
					background: #F4F4F4;
					border: 1px solid #ECECEC;
					font-size: 12px;
					font-family: Microsoft YaHei;
					font-weight: 400;
					color: #595959;
					margin-bottom: 15px;
					margin-right: 20px;
					margin-top: 20px;
				}
				.gate{
					border: 1px solid #FF5E00;
				}
			}
		}
	}
}
.pro-fund{
	height: 95px;
	background: #FF5E00;
	display: flex;
	align-items: center;
	h1{
	
		font-size: 40px;
		font-family: PingFang SC;
		font-weight: 800;
		color: #FFFFFF;
