	}
}
 .adr{
	display: flex;
	position: fixed;
	bottom: 0;
	width: 720px;
	height: 110px;
	background: #FFFFFF;
	.lt{
		width: 330px;
		ul{
			display: flex;
			justify-content: space-evenly;
			li{
				a{
					height: 110px;
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: center;
				}
				img{
					width: 34px;
					height: 34px;
