				}
				p{
					
					font-size: 24px;
					font-family: PingFang SC;
					font-weight: 400;
					color: #BDBDBD;
					

				}
			}
		}
	}
	.rt{
		width: 390px;
		display: flex;
		.h-button{
			width: 50%;
			height: 110px;
			background: #F29F25;
