  },
  data() {
    return {
		active: 1,
		id:'',
		file:'',
		ruleForm:{
			email:'',
			password:''
		}
    
    };
  },mounted() {
  	this.id = this.$route.query.id

  }
  ,methods:{
	loginForm(){
				
				if(this.ruleForm.phone == ''){
				
					Toast('请输入邮箱');
					return
				}
				if(this.ruleForm.password == ''){
					Toast('请输入密码');
					return
				}
				
				
			this.$axios({
			      method: "post",
			      url: "service.php?action=users_emailLogin",
			      data: this.ruleForm,  
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==1){
									
						localStorage.setItem("user", JSON.stringify(data.data));
						Toast(data.message);
					this.$router.push({ path: "/" });	
			    		
			    	}else{  
									Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},checkPhone(phone){ 
				if(!(/^1[3456789]\d{9}$/.test(phone))){ 
					Toast('请填写正确的手机号');
					
					return false; 
				} else{
					return true
				}
			}
  }
};
