<template>
  <div class="login">
   <Title titleName='提交订单' iconLeft="back"/>


	<div class="order">
	<router-link to="/user/address">
	<div class="can4 name">
		
		<div>
			<div v-if="is_add">
				<p>{{address.name}}  {{address.phone}}</p>
				<p>{{address.address}}</p>
			</div>
		<b v-else>请选择收货地址</b>
		</div>
		<div class="pl"><img src="../../assets/ip.png"></div>
		
	</div>
	</router-link>
<div class="pro-l2">

	<div class="can1">
		<div><b>商品信息</b></div>
	
	</div>

	<van-row class="can2" gutter='20' v-for="item in list">
		<van-col span="8"><img :src="item.image"></van-col>
		<van-col span="16">
			<p>{{item.title}}</p>
			<p>规格：{{item.key_vals}}</p>
			<p><span>x{{item.num}}</span><span>￥{{item.price_sale}}</span></p>
		</van-col>
	</van-row>
<!-- 	<div class="can">
		
		<div>
		
		<b>優惠券</b>
		</div>
		<div class="pl"><span v-if="info.yhq_list.length == 0">暫無可用優惠券</span><span @click="is_show_coupon = true" v-else-if="params_info.yhq_id == 0">{{info.yhq_list.length}}张可用</span><span @click="is_show_coupon = true" v-else>-￥{{coupon_info.jian}}</span><img src="../../assets/ip.png"></div>
		
	</div>
	<div class="coupon" v-if="is_show_coupon">
