		<div class="pl" @click="use_jifen">
			<img v-if="is_use_jifen" src="../../assets/k2.png">
			<img v-else src="../../assets/k1.png">
		</div>
		
	</div>
	<div class="can">
		
		<div>
		
		<b>運費</b>
		</div>
		<div class="pl">
			￥{{info.yunfei || 0}}
		</div>
		
	</div> -->
</div>
<div class="can4">
		
		<div>
		
		<b>配送方式</b>
		</div>
		<div class="pl"><span>快递配送</span></div>
		
	</div>
	<div class="can4">
		
		<div>
		
		<b>支付方式</b>
		</div>
		<div class="pl" @click="showPicker=true"><span>{{pay_text}}</span><img src="../../assets/ip.png"></div>
		
	</div>
	<div class="can5" v-if="info.ischufang==1">
		
		<div class="l1">
		
		<b>处方药必填</b><span>最多上传6张</span>
		</div>
	<van-uploader
	  v-model="uploader_FY"
					:after-read="afterRead"
	  :before-delete="delUploadImg_FY"
	  accept="image/*"
	  multiple
	/>
		
	</div>
	<div class="h-fan">
	注：48小时内发货，详情咨询{{et.com_hotline}}。除药品质量外，
	      药品一经销售，不得退换。</div>
	</div>

	<div class="adr">
	<div>合计：<b>￥{{get_money(info.order_price,coupon_info.jian,info.jifen_pay.money)}}</b></div>
			<van-button color="#FF5E00"  @click="pay">去支付</van-button>
	</div>
	<van-popup v-model="showPicker" position="bottom">
	  <van-picker
	    show-toolbar
	    :columns="pad"
		value-key="text"
	    @confirm="onConfirm"
	    @cancel="showPicker = false"
	  />
	</van-popup>
	
	
  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import { Toast } from 'vant';	
export default {
  components: {
    Title,
    CardList

  },
  data() {
    return {
		active: 1,
		id:'',
		file:'',
		is_add:false,
		address:'',
		 et:'',
		pad:'',
		promiseArr:[],
		showPicker:false,
		pay_text: '微信支付',
		uploader_FY:[],
		type_list: [{
				text: 'PAYPAL支付',
				value: 1
			},{
				text: '微信支付',
				value: 2
			}
		
		], 
		is_show_coupon:false,
		pay_type: 1,
		list:[],
		product_info:[],
		info:'',
		params_info: {
			product_info: [],
			address_id: '',
			peisong_type: 3, 
			remark: '',
			tuan_type: 0,
			yhq_id: 0,
			belong:'',
			korean_code:''
		},
		is_use_jifen:true,
		coupon_info: {
			id: 0,
			discount: 0
		},
		ruleForm:{
			email:'',
			password:''
		}
    
    };
  },mounted() {
  	this.id = this.$route.query.id
	this.order()
	this.ets()
	this.sandCase()
	this.sandPay();

  },methods:{
	order(){
		if(window.localStorage.getItem('address')){
		  this.address = JSON.parse(window.localStorage.getItem('address'));
		  this.is_add=true
		}
	
		this.list =JSON.parse(window.localStorage.getItem('order_info'))
		this.product_info=JSON.parse(this.$route.query.product_info)
	
		
	},
	ets(){
					  
		this.$axios({
		  method: "post",
		  url: "service.php?action=kehu_contactus",
		  data: {},  
		}).then((res) => {
			let data=res.data;
	
			if(data.code==1){
						
				this.et=data.data;
							
			
			}
		 
		});
	
	},
	loginForm(){
				
				if(this.ruleForm.phone == ''){
				
					Toast('请输入邮箱');
					return
				}
				if(this.ruleForm.password == ''){
					Toast('请输入密码');
					return
				}
				
				
			this.$axios({
			      method: "post",
			      url: "service.php?action=users_emailLogin",
			      data: this.ruleForm,  
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==1){
									
						localStorage.setItem("user", JSON.stringify(data.data));
						Toast(data.message);
					this.$router.push({ path: "/" });	
			    		
			    	}else{  
									Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			}, 
			pay() {
				
				
				if (!this.is_add) {
					Toast('收货地址不能为空');
					return false;
				}
				if(this.info.ischufang==1&&this.promiseArr.length==0){
									 Toast("请上传处方照片");
									 return false;
				}
				
				this.$axios({
				  method: "post",
				  url: "service.php?action=orders_create",
				  data: {address_id:this.address.id,xdpaytype:this.pay_text,peisong_type:'快递配送',product_info:JSON.stringify(this.product_info),cfpics:JSON.stringify(this.promiseArr)}, 
				}).then((res) => {
					let data=res.data;
							  
				if(data.code==1){
										
					Toast(data.message);					
							
			this.sandRed(data.order_id)
				    		
				    	}else{  
										Toast(data.message);	
										      
									}
				 
				});
				
				
			// 	API.getJson('orders_create', this.params_info).then((res) => {
			// 		console.log(res);
			// 		if (res.code == 1) {
			// 			let id = res.order_id
			// 			this.order_id = id
			// 			if (this.is_use_jifen) {
			// 				this.jifen_pay(id)
			// 			}
			// 			if (this.pay_type == 1) {
			// 				//微信支付
			// 				this.$m.loading(6000)
			// 				this.wechat_pay(id)
			// 			} else {
			// 				this.is_show_pay = true
			// 			}
			
			// 		} else {
			// 			msg(res.message)
			// 		}
			// 	})
			
			},
			
			onConfirm(value) {
     this.pay_text=value
	 this.pay_type =value
	 this.showPicker=false
    },	get_money(val1, val2, val3) {
				if(this.is_use_jifen){
					val2 = val2 || 0
					if (Number(val1) > (Number(val2)+Number(val3))) {
						return (Number(val1) - Number(val2)-Number(val3)).toFixed(2)
					}
					return 0;
				}else{
					val2 = val2 || 0
					if (Number(val1) > Number(val2)) {
						return (Number(val1) - Number(val2)).toFixed(2)
					}
					return 0;
				}
				
			},checkPhone(phone){ 
				if(!(/^1[3456789]\d{9}$/.test(phone))){ 
					Toast('请填写正确的手机号');
					
					return false; 
				} else{
					return true
				}
			}, 	user_coupon(item) {
				this.coupon_info = item
				this.params_info.yhq_id = item.id
				this.is_show_coupon = false
			}, sandCase(){
			  	this.$axios({
			  	  method: "post",
			  	  url: "service.php?action=orders_getPayInfo",
			  	  data: {address_id:this.address.id,product_info:JSON.stringify(this.product_info)},  
			  	}).then((res) => {
			  		let data=res.data;
			  
			  		if(data.code==1){
			  					
			  			this.info=data.data;
						
			  		
			  		}
			  	 
			  	});
			  
			  }, afterRead(file) {
		
		 this.promiseArr=[]
			
		this.uploader_FY.forEach(v => {
		
		 this.$axios({
		    	  method: "post",
		    	  url: "service.php?action=index_upfile",
		    	  data: {img:v.content},  
		    	}).then((res) => {
		    		let data=res.data;
		    	
		    		if(data.code==1){
		    			
		    		Toast(data.message);
		 		 this.promiseArr.push(data.image);
		    		}else{  
		    			Toast(data.message);
		    			      
		    		}
		    	 
		    	});
		 
		 
		 
		});
		
		
	
	    }, sandPay(){
			  	this.$axios({
			  	  method: "post",
			  	  url: "service.php?action=kehu_aps",
			  	  data: {},  
			  	}).then((res) => {
			  		let data=res.data;
			  
			  		if(data.code==1){
			  	
			  							
			  			this.pad=data.data;
			  			
			  			
			  		}
			  	 
			  	});
			  
			  },	use_jifen() {
			
				this.is_use_jifen = !this.is_use_jifen
			},	  sandRed(id){
				
				if(this.pay_text=='微信支付'){
					
					 	this.$axios({
					 	  method: "post",
					 	  url: "service.php?action=orders_wxH5Pay",
					 	  data: {order_id:id},  
					 	}).then((res) => {
					 		let data=res.data;
					 
					 		if(data.code==1){
								
							this.code=data.code_url
							this.add=true
							//this.pizza(id)
								//alert(data.data)
					 			//window.location.href=data.data.link	
									
					 		}
					 	 
					 	});
									
				}
				
				if(this.pay_text=='支付宝支付'){
					
					 	this.$axios({
					 	  method: "post",
					 	  url: "service.php?action=orders_aliwpay",
					 	  data: {order_id:id},  
					 	}).then((res) => {
					 		let data=res.data;
					 
					 		if(data.code==1){
								
							this.code=data.data
						const div = document.createElement('div')
						  div.innerHTML = this.code
						       document.body.appendChild(div)
						       document.forms[0].submit()
									
					 		}
					 	 
					 	});
									
				}
				if(this.pay_text=='线下支付'){
					 this.$router.push({ path: "/user/success?id="+id });	
									
				}
				if(this.pay_text=='医保卡支付'){
				this.$router.push({ path: "/product/rider" });			
				}
				if(this.pay_text=='银行卡支付'){
				Toast('请选择其他支付方式');	
				}
				if(this.pay_text=='余额支付'){
					this.$axios({
					  method: "post",
					  url: "service.php?action=orders_yuePay",
					  data: {order_id:id},  
					}).then((res) => {
						let data=res.data;
										 
						if(data.code==1){
									
							Toast(data.message);	
							this.$router.push({ path: "/product/rider?id="+id });	
						
						}else{
							Toast(data.message);	
						}
					 
					});
									
				}
			
			  
			  },
  }
};
</script>
<style lang="scss" scoped>
