		}
	}
	.coupon{
		li{
			padding: 20px;
			border-bottom: 1px solid #DDDDDD;
			display: flex;
			justify-content: space-between;
		}
	}

}

	.can4{
		margin-top: 20px;
		padding: 0 20px;
		background: #FFFFFF;
		display: flex;
		justify-content: space-between;
		height: 100px;
		align-items: center;
		.pl{
			display: flex;
			align-items: center;
			img{
				width: 30px;
				height: 30px;
			}
			input{
				border: none;
				text-align: right;
				width: 600px;
			}
		}
	}
	.can5{
		margin-top: 20px;
		padding: 0 20px;
		background: #FFFFFF;
		align-items: center;
		padding-top: 30px;
		.l1{
			margin-bottom: 30px;
			
			span{
				font-size: 24px;
				color: #BBBBBB;
				margin-left: 20px;
			}
			
		}
		
	}
	.h-fan{
		
		font-size: 24px;
		font-family: PingFang SC;
		font-weight: 400;
		color: #E42626;
		margin: 20px;
		

	}
 .pro-tit{
	 background: #FFFFFF;
	 text-align: center;
	 line-height: 40px;
	 padding: 20px 0;
	 h3{
		 font-size: 36px;
	 }
	 p{
		 font-size: 24px;
	 }
 }
 .adr{
 	display: flex;
 	align-items: center;
	justify-content: space-between;
 	position: fixed;
 	bottom: 0;
 	width: 680px;
 	height: 68px;
 	background: #FFFFFF;
 	font-size: 28px;
 	box-shadow: 0px -4px 10px rgba(0, 0, 0, 0.06);
	padding: 20px;
	b{
		font-size: 36px;
		color: #E42626;
	}
 }
</style>