<template>
  <div class="login">
     <Title titleName='预定专区' iconLeft="back"/>

	<div class="pro-tit">
		<img class="it1" src="../../assets/logo2.png" />
		<img class="it2" src="../../assets/logo3.png" />
	</div>


	<div class="login_user" >
		<div class="user_input">
			
		<div> </div> <input v-model="ruleForms.name" placeholder="请输入姓名" />
		</div>
		<div class="user_input">
			
		<div> </div> <input v-model="ruleForms.phone" placeholder="请输入手机号" />
		</div>
	
	<div class="user_input">
		
	<div> </div> <input v-model="ruleForms.content" placeholder="请输入您需要的药品名称，厂家，数量" />
	</div>
	
	</div>

	<div class="login_bt">
	
		<van-button color="#FF5E00"   @click="loginForms()" block>留言预定</van-button>
	</div>
	
	
	
	
	
	
  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import { Toast } from 'vant';	
export default {
  components: {
    Title,
    CardList

  },
  data() {
    return {
		active: 1,
		id:'',
		file:'',
		ruleForm:{
			phone:'',
			pwd:''
		},
		ruleForms:{
			phone:'',
			content:'',
			name:''
			
		}
    
    };
  },mounted() {
  	this.id = this.$route.query.id

  },methods:{
	loginForm(){
				
				if(this.ruleForm.phone == ''){
				
					Toast('请输入手机号');
					return
				}
				if(this.ruleForm.pwd == ''){
					Toast('请输入密码');
					return
				}
				
				
			this.$axios({
			      method: "post",
			      url: "service.php?action=users_phoneLogin",
			      data: this.ruleForm,  
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==1){
									
						localStorage.setItem("user", JSON.stringify(data.data));
						Toast(data.message);
					this.$router.push({ path: "/" });	
			    		
			    	}else{  
									Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},loginForms(){
				
				if(this.ruleForms.phone == ''){
				
					Toast('请输入手机号');
					return
				}
				if(this.ruleForms.name == ''){
					Toast('请输入姓名');
					return
				}
				
				
			this.$axios({
			      method: "post",
			      url: "service.php?action=users_userydly",
			      data: this.ruleForms,  
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==1){
									
						
						Toast(data.message);
				
			    		
			    	}else{  
									Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},pad(){
				
				
				
				
			this.$axios({
			      method: "post",
			      url: "service.php?action=users_sendSms",
			      data: {phone:this.ruleForms.phone},  
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==1){
									
					Toast(data.message);
			    		
			    	}else{  
									Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},checkPhone(phone){ 
				if(!(/^1[3456789]\d{9}$/.test(phone))){ 
					Toast('请填写正确的手机号');
					
					return false; 
				} else{
					return true
				}
			}
  }
};
</script>
