<template>
  <div class="index">
   <Title titleName='活动快讯' iconLeft="back"/>
	




	<div class="index-product">
	
		<div class="con">
			<ul>
				<li v-for="it in news">
					<h3><router-link :to="'/index/details?id='+it.id"> {{it.title}}</router-link></h3>
					
						 <van-row>
							 <van-col span='10'><router-link :to="'/index/details?id='+it.id"><div class="tu"><img :src="it.originalPic"  /></div></router-link></van-col>
							 <van-col span='14'> <router-link :to="'/index/details?id='+it.id">
							 <div class="txt">
							
								
								 <p class="money">{{it.short}}</p>
								  <div class="mon">{{it.dtTime}}</div>
								  <div class="qi">
									  点击查看 <img src="../../assets/qi.png" />
								  </div>
							 </div>
						
						 </router-link>	</van-col>
						 </van-row>
			
					
				</li>
			
			</ul>
			<div class="more">
				
				<div class="button" @click="more">{{tip}}</div>
				
			</div>
		</div>
		
	</div>

	
  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";

export default {
  components: {
    Title,
    CardList
  },
  data() {
    return {
		title:'',
		channel_id:2,
		images:[],
		info:'',
		page:1,
		news:[],
		notice:[],
		name:'',
		hans:'',
		sfznum:'',
		phone:'',
		tip:''

    };
  },  mounted() {

		
		this.sandMate();
		},methods: {
			more(){
				this.page+=1;
				this.sandMate()
			},
		sandMate(){
			
			this.$axios({
			  method: "post",
			  url: "service.php?action=index_newsList",
			  data: {channel_id:1,page:this.page},  
			}).then((res) => {
				let data=res.data;
		
				if(data.code==1){
							
				
					if(data.data.length==0&&this.page==1){
						this.tip='暂无内容'
						return
					}
					
					if(data.data.length==0&&this.page!=1){
						this.tip='没有更多了'
						return
					}
					this.tip='查看更多'
					this.news=this.news.concat(data.data);
					
					
					
				}
			 
			});
		
		},sandAlt(){
				this.news=[]
				this.sandMate()
			
			},sandHans(){
			
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_channel",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
			
							data.data.forEach((item, index) => {
								if(this.channel_id==item.id){
									this.title=item.title
									this.hans=item.channels
									if(this.hans.length>0){
										this.channel_id=this.hans[0].id
									}
									this.sandMate();
									return
								}
							})			
					
						
					}
				 
				});
			
			}
		}
};
</script>
