<template>
  <div class="login">
	  
	<Title v-if="con.offer_id&&con.offer_status==0" titleName='修改报价' iconLeft="back"/>
   <Title v-if="!con.offer_id" titleName='我的报价' iconLeft="back"/>
   <Title v-if="con.offer_status==1" titleName='出票中' iconLeft="back"/>
	

	<div class="mem-con">
		<div class="tag" v-if="s">
			<img src="../../assets/tis.png" />
		{{m}}分{{s}}秒</div>
		<div class="mem-l1">
			
		<div class="tit">{{con.movie_name}}</div>
		<p>{{con.show_date}} {{con.show_time}}~{{con.close_time}}</p>
			<p>{{con.cinemas}}</p>
			<p>{{con.halls}}</p>
			<p>{{con.seat_names}}</p>
		</div>
	
		

	</div>
	

			<div class="card-color">
			
				
			
				<div class="leaf">
					<div class="dont">
						订单号
					</div>
					<div class="fit">
					{{con.order_no}}
						
					</div>
				</div>
				<div class="leaf">
					<div class="dont">
						类型
					</div>
					<div class="fit">
				<span class="font-col" v-if="con.seat_flag==0">普通座</span><span class="font-col" v-if="con.seat_flag==1">情侣左</span><span class="font-col" v-if="con.seat_flag==2">情侣右</span><span class="hand">|</span><span class="font-col" v-if="con.accept_seats==1">可调座</span><span class="font-col" v-if="con.accept_seats==0">不可调座</span>
						
					</div>
				</div>
			<div class="leaf">
				<div class="dont">
					市场价<span v-if="con.offer_id">/我的报价</span>
				</div>
				<div class="fit">
					<span class="font-col">{{con.market_price}}元</span>
				
					<span class="hand">|</span><span class="font-col">{{con.offer_amount}}元</span>
				</div>
				
			</div>
			<div class="leaf">
				<div class="dont">
					地址
				</div>
				<div class="fit">
				{{con.address}}
					
				</div>
			</div>
			<div class="bao" v-if="con.offer_status==0">
				<div class="dont">
					建议报价区间
					<p>{{con.min_price}}-{{con.max_price}}元</p>
				</div>
				<div class="fit">
					<input type="text" v-model="ruleForm.price" placeholder="请输入单价"/>
				<span>x{{con.ticket_count}}</span>
				</div>
			</div>
			</div>
	<div class="li-give" v-if="con.offer_status==1">
		<div class="tit">取票码<img src="../../assets/noodles.png" @click="del=true"></div>
		<div class="give">
			<div class="bear" v-if="but.images">
				 <img :src="but.images" />
			</div>
			<div class="bear" v-else>
				<img src="../../assets/edu.png" />
				请上传出票图
			</div>
		</div>
	</div>
	<div v-if="con.offer_status==0">
	<div class="hb-button" v-if="con.offer_id" @click="loginForm">确认修改</div>
	<div class="hb-button" v-else @click="loginForm">立即报价</div>
	<div class="fight" v-if="con.offer_id" @click="fight">撤回报价</div>
	</div> 
	<div class="fast" v-if="con.offer_status==1">
		<!-- <div class="h-button" @click="fight">退回</div> -->
		<div class="h-button ad" @click="honor">释放座位</div>
		
	</div>
	<div class="fast" v-if="con.offer_status==1">
		<div class="h-button cloud" @click="ticket">出票</div>
		
		<div class="h-button ad" @click="white">转票</div>
	</div>
	<van-dialog v-model="del" @confirm='pick' show-cancel-button>
	 <div class="link">
		 <div class="want">
			 <img v-if="but.images" :src="but.images" />
			 <van-uploader v-else :after-read="afterRead">
			  <van-button round block native-type="submit" >
			   上传出票图
			  </van-button>
			 </van-uploader>
		 </div>
	
		 <div class="gand">
			 <ul>
				 <li>
					 <input type="text" v-model="but.code" placeholder="取票码(必填)" />
				 </li>
				 <li>
				 	 <input type="text" v-model="but.valid_code" placeholder="验证码(非必填)" />
				 </li>
			 </ul>
		 </div>
	 </div>
	</van-dialog>
  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import Bar from '@/components/bar/bar'
import { Toast } from 'vant';	
export default {
  components: {
    Title,
    Bar

  },
  data() {
    return {
		checked:false,
		user:'',
		active: 1,
		id:'',
		m:0,
		s:0,
		file:'',
		con:'',
		del:false,
		but:{
			order_no:'',
			images:'',
			code:'',
			valid_code:''
		},
		ruleForm:{
			price:'',
			order_no:''
		}
    
    };
  },mounted() {

  	this.id = this.$route.query.id
  	this.funder()
this.sandMate();
  },methods:{
	sandMate(){
		this.$axios({
		  method: "post",
		  url: "service.php?action=users_userinfo",
		  data: {},  
		}).then((res) => {
			let data=res.data;
	
			if(data.code==1){
		
								
				this.user=data.data;
					
				
			}
		 
		});
	
	}, ctime(){
				     //获取当前时间
					
				               
				                 var now = Math.round(new Date().getTime()/1000)
				                 //设置截止时间
								//  var endDate = new Date("2021-1-18 23:23:23");
								  
				                var endDate = new Date(this.con.expire_time);
				                 var end = endDate.getTime();
								
				                 //时间差
				                 var leftTime = end - now;
				                 //定义变量 d,h,m,s保存倒计时的时间
								
				                 if (leftTime >= 0) {
									 
				                 
				                     this.m = Math.floor(leftTime / 1000 / 60 % 60);
				                     this.s = Math.floor(leftTime / 1000 % 60);
				                 }
								 
				                 console.log(this.s);
				                 //递归每秒调用countTime方法，显示动态时间效果
				                 setTimeout(this.ctime, 1000);
			 },afterRead(file) {
		
		 this.$axios({
		   method: "post",
		   url: "stores/upload",
		   data: {file:file.content}
		 }).then((res) => {
	
		 	let data=res.data;
		 	if(data.code==200){
				Toast(data.message);
		 		this.but.images=data.data.img
		 	}else{
		 		Toast(data.message);
		 		}
		  
		 });
		 
	
	    },ticket(){
				
			this.but.order_no=this.id
				
			this.$axios({
			      method: "post",
			      url: "stores/create_ticket",
			      data: this.but
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==200){
									
					this.$router.back()
						Toast(data.message);
					
			    		
			    	}else{  
					Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},pick(){
				
			this.but.order_no=this.id
				
			this.$axios({
			      method: "post",
			      url: "stores/create_ticket",
			      data: this.but
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==200){
									
					this.$router.back()
						Toast(data.message);
					
			    		
			    	}else{  
					Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},loginForm(){
				
				if(this.ruleForm.price == ''){
				
					Toast('请输入单价');
					return
				}
				
				
			this.$axios({
			      method: "post",
			      url: "stores/create_offer",
			      data: this.ruleForm,  
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==200){
									
					this.$router.back()
						Toast(data.message);
					
			    		
			    	}else{  
					Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},white(){
			
				
			this.$axios({
			      method: "post",
			      url: "stores/backout",
			      data: {order_no:this.id}
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==200){
									
					this.$router.back()
						Toast(data.message);
					
			    		
			    	}else{  
					Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},fight(){
			
				
			this.$axios({
			      method: "post",
			      url: "stores/cancel_offer",
			      data: {offer_id:this.con.offer_id}
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==200){
									
					this.$router.back()
						Toast(data.message);
					
			    		
			    	}else{  
					Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},honor(){
			
				
			this.$axios({
			      method: "post",
			      url: "stores/release-seat",
			      data: {order_no:this.id}
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==200){
									
					//this.$router.back()
						Toast(data.message);
					
			    		
			    	}else{  
					Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},funder(){
			   this.$axios.get('stores/order_info',{params:{
				  order_no:this.id
			   }}).then((res)=>{
				   let data=res.data;
				   if(data.code==200){
					   this.con=data.data
					   
					   this.ruleForm.order_no=this.id
					 this.ctime()
				   }
			   })
			   

			  				 
	}
  }
};
</script>
<style lang="scss" scoped>
