	 		 padding: 50px 0;
	 		 padding-right: 30px;
	 		 margin-left:30px;
	 		 height: 40px;
	 		 font-size: 28px;
	 		 color: #000000;
	 		 a{
	 			 display: flex;
	 			 justify-content: space-between;
	 			 align-items: center;
	 			 width: 100%;
	 			 color: #000000;
	 		 }
	 		 .dont{
	 			 display: flex;
				 flex-direction: column;
	 			 align-items: center;
	 			 img{
	 				 width: 32px;
