	 				 margin-right: 15px;
	 			 }
				 p{
					 color: #FF5E00;
				 }
	 		 }
	 		 .fit{
	 			 
	 			
				 input{
					 width: 140px;
					 border: none;
					 height:60px;
					 border-bottom: 1px solid #EAEAEA;
				 }
				 span{
					 color: #FF5E00;
					 margin-left: 20px;
				 }
	 		 }
	 		 .font-col{
	 		 	 color: #FF5E00;
	 		 }
	 		 .hand{
	 			 margin: 0 10px;
	 		 }
	 }
 }
 .hb-button{
	margin: 30px;
	 height: 100px;
	 background: #FF5E00;
	 border-radius: 50px;
	 color: #FFFFFF;
	 font-size: 28px;
	 text-align: center;
	 line-height: 100px;

 }
