  <div class="index">
    <ind />
	<van-search v-model="con" shape="round"   @search="onSearch" placeholder="输入药品名称搜索" />
	<div class="type-con">
		<van-sidebar v-model="activeKey"  @change="onChange">
		  <van-sidebar-item  v-for="item in plus"  @click="center(item)" :title="item.title" />
		
		</van-sidebar>
		<div class="tend">
			<h3><span> —— </span>{{git.title}}<span> —— </span></h3>
			<ul>
				<li v-for='item in git.channels' ><router-link :to="'/index/product?id='+item.id">
					<img :src="item.originalPic" />
					<p>{{item.title}}</p> </router-link>	
				</li>
			
			</ul>
		</div>
	</div>
	

	


	
 <Bar but='2'/>
  </div>
</template>
<script>
import ind from "@/common/ind";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import Bar from '@/components/bar/bar'
export default {
  components: {
    ind,
    CardList,
	Bar
  },
  data() {
    return {
		activeKey: 0,
		images:[],
		info:'',
		git:[],
		page:1,
		con:'',
		channel_id:0,
		news:[],
		notice:[],
		name:'',
		hans:'',
		sfznum:'',
		phone:'',
		plus:'',

    };
  },  mounted() {
	 
	this.name = window.localStorage.getItem('name')
	this.sfznum = window.localStorage.getItem('sfznum')
	this.phone = window.localStorage.getItem('phone')
	this.sandAlt()
	this.sandChan()
	this.sandHans();
		this.sandMate();
		this.sandCase();
		},methods: {
			onChange(index) {
			      Notify({ type: 'primary', message: index });
			    },
			 onSearch(val) {
			      this.$router.push({ path: "/index/product?con="+this.con });	
			    },sandChan(){
			
				
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_channel",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
				
										
						this.plus=data.data;
						this.git=this.plus[0]
						
						
					}
				 
				});
			
			},center(item){
				this.git=item
			},
		sandMate(){
			this.$axios({
			  method: "post",
			  url: "service.php?action=product_plist",
			  data: {page:this.page,channel_id:this.channel_id},  
			}).then((res) => {
				let data=res.data;
					
				if(data.code==1){
					
					
				
					this.news=data.data;
					
					
					
				}
			 
			});
		
		},sandCase(){
				this.$axios({
				  method: "post",
				  url: "/api/noticelist",
				  data: {page:1},  
				}).then((res) => {
					let data=res.data;
					
					if(data.code==200){
						
						this.notice=data.data[0];
						
						
					
					}
				 
				});
			},sandAlt(){
				this.$axios({
				  method: "post",
				  url: "service.php?action=index_index",
				  data: {type:2},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
				
										
						this.images=data.data.banners;
						
						
					}
				 
				});
			
			},sandHans(){
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_channel",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
				
										
						this.hans=data.data;
						
						
					}
				 
				});
			
			}
		}
};
</script>
