		page:1,
		news:[],
		notice:[],
		name:'',
		hans:'',
		sfznum:'',
		phone:'',
		tip:''

    };
  },  mounted() {
	
	// let hh={token:'afdf759ed4f54a26ba649d492167ff95'}
	// localStorage.setItem('user',JSON.stringify(hh))
	// let hh={token:'118995dfd864bb2ccc0bc35c4706c9bb'}
	// localStorage.setItem('user',JSON.stringify(hh))
	// let hh={token:'7d2b7212c7b6247c604f4cdfd9949cd7'}
	// localStorage.setItem('user',JSON.stringify(hh))
	this.sandRed()
	this.sandMate()
	
		
		
		},methods: {
			more(){
				this.page+=1;
				this.sandMate()
			},ctime(id){
				     //获取当前时间
					
								 var now= Math.round(new Date().getTime()/1000).toString();
								
				               //  var now = date.getTime();
				                 //设置截止时间
								//  var endDate = new Date("2021-1-18 23:23:23");
								  
				               // var endDate = new Date(id);
				               //  var end = endDate.getTime();
				                 //时间差
				                 var leftTime =  id-now ;
				                 //定义变量 d,h,m,s保存倒计时的时间
								 
				                 if (leftTime >= 0) {
									 
				                   let d = Math.floor(leftTime / 1000 / 60 / 60 / 24);
									 let h = Math.floor(leftTime / 1000 / 60 / 60 % 24);
				                    let m = Math.floor(leftTime  / 60 % 60);
				                     let s = Math.floor(leftTime % 60);
									
									//return d+":"+h+":"+m+":"+s
									return m+"分"+s+"秒"
				                 }
							
				                 //递归每秒调用countTime方法，显示动态时间效果
				                 
			 },sandRed(){
				 if(this.$route.query.id){
					 this.id = this.$route.query.id
				 }
				
			  this.$axios({
			    method: "post",
			    url: "stores/info",
			    data: {},  
			  }).then((res)=>{
			      let data=res.data;
			      let user = JSON.parse(window.localStorage.getItem('user'));
			      if(data.code==200){
			      	data.data.store_info.token=user.token
			      	localStorage.setItem('user',JSON.stringify(data.data.store_info))
			      	this.user=data.data.store_info
			      	
			      
			      }
			   })
			   
			  }, 
		
		sandMate(){
			
			
			
			// this.user={
			// 	avatar: "https://thirdwx.qlogo.cn/mmopen/vi_32/h1qencwsqYMDoyLDHBbOXXkxibhhRpSz5anKta3bewVBqrVoDqqhwXrrn7vhuMk8ibI4VvlHoRJFQZIlkwwDSGOw/132",
			// 	city: "",
			// 	country: "",
			// 	created_at: "2021-07-28 16:35:36",
			// 	id: 11,
			// 	nickname: "桔梗",
			// 	openid: "oHb-H5tTUblVV-UDzE_MrWZ3hsaU",
			// 	province: "",
			// 	sex: 0,
			// 	store_city: "",
			// 	store_country: "",
			// 	store_info: null,
			// 	store_level: 0,
			// 	store_level_txt: "",
			// 	store_name: "",
			// 	store_phone: "",
			// 	store_pos_str: "",
			// 	store_province: "",
			// 	store_state: 0,
			// 	unionid: "",
			// 	updated_at: "2021-07-28 16:35:36",
			// 	token:'a5451fb53de70876717091c23a37ac60'
			// }
			//this.user = JSON.parse(window.localStorage.getItem('user'));
			this.news=[]
			this.$axios.get('stores/order_list',{params:{
							  type:this.id,keywords:this.keywords
			}}).then((res)=>{
						let data=res.data;
						if(data.code==200){
							
						
							if(data.data.list.data.length==0&&this.page==1){
								this.tip='暂无内容'
								return
							}
							
							if(data.data.list.data.length==0&&this.page!=1){
								this.tip='没有更多了'
								return
							}
							this.tip='查看更多'
							this.news=this.news.concat(data.data.list.data);
							if(this.id==1){
								this.news.forEach(item=>{
									item['hand'] = this.ctime(item.expire_time)
								})
								
								 setTimeout(this.sandMate, 1000);
							}
							
							
						}
			})
			
		// 	this.$axios({
		// 	  method: "get",
		// 	  url: "stores/order_list",
		// 	 data:{}
		// 	}).then((res) => {
		// 		let data=res.data.list;
		
		// 		if(data.code==200){
							
				
		// 			if(data.data.length==0&&this.page==1){
		// 				this.tip='暂无内容'
		// 				return
		// 			}
					
		// 			if(data.data.length==0&&this.page!=1){
		// 				this.tip='没有更多了'
		// 				return
		// 			}
		// 			this.tip='查看更多'
		// 			this.news=this.news.concat(data.data);
					
					
					
		// 		}
			 
		// 	});
		
		},sandAlt(){
				this.news=[]
				this.sandMate()
			
			},wind(id){
			   this.$axios({
			     method: "post",
			     url: "stores/ignore_order",
			     data: {order_no:id.order_no},  
			   }).then((res)=>{
			       let data=res.data;
			      
			       if(data.code==200){
			      Toast(data.message);
			       	this.news=[]
			       	this.sandMate()
			       
			       }else{
					   Toast(data.message);
				   }
			    })
			   
			   },  confirm(item){
			   	this.$axios({
			   	  method: "post",
			   	  url: "service.php?action=orders_qrshouhuo",
			   	  data: {orderId:item},  
			   	}).then((res) => {
			   		let data=res.data;
			   	
			   		if(data.code==1){
			   			
			   		Toast(data.message);
					
					 this.news=[]
			this.sandMate();
			   		}else{  
			   			Toast(data.message);
			   			      
			   		}
			   	 
			   	});
			   
			   },sandHans(){
			
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_channel",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
			
							data.data.forEach((item, index) => {
								if(this.channel_id==item.id){
									this.title=item.title
									this.hans=item.channels
									if(this.hans.length>0){
										this.channel_id=this.hans[0].id
									}
									this.sandMate();
									return
								}
							})			
					
						
					}
				 
				});
			
			}
		}
};
</script>
