	<img src="../../assets/user_service.png" />
	<p>我的余额</p>
	<div class="qis">
		<div></div>
		<div class="ge"></div>
		<h3>￥{{info.money}}</h3>
		
		<div class="cong"><router-link to="/user/coupon">充值</router-link></div>
	</div>
	
</div>

<div class="pro-tab">

	<van-tabs color="#E42626"  title-active-color="#E42626" v-model="channel_id" @click="sandAlt" :ellipsis="false">
	 <van-tab title="全部" name="0"></van-tab>
	 <van-tab title="支出" name="1" ></van-tab>
		 <van-tab title="收入" name="2" ></van-tab>
		
				
	  
	</van-tabs>
</div>



	<div class="index-product">
		
		<div class="con">
			<ul>
				<li v-for="it in news">
					
					<div class="can">
						
						<div>
						 创建：{{it.dtTime}}
						</div>
						<b>{{it.remark}}</b>
						
					</div>
				<van-row class="can2" gutter='20' v-for="item in it.products">
					<van-col span="8"><img :src="item.image"></van-col>
					<van-col span="16">
						<p>{{item.title}}</p>
						<p>规格：{{item.key_vals}}</p>
						<p><span>x{{item.num}}</span><span>￥{{item.price_sale}}</span></p>
						<div v-if="it.status==4&&item.ifpingjia==0"><router-link :to="'/user/comment?id='+item.id+'&rt='+it.id">写评价</router-link></div>
					</van-col>
				</van-row>
			<van-row class="can3">
				<van-col span="24">
					<p>订单号：{{it.orderId}}</p>
					<p>{{it.money}}元</p>
				</van-col>
				
				
			</van-row>
				
				</li>
			
			</ul>
			<div class="more">
				
				<div class="button" @click="more">{{tip}}</div>
				
			</div>
		</div>
		
	</div>



  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import { Toast } from 'vant';	
export default {
  components: {
    Title,
    CardList
  },
  data() {
    return {
		title:'',
		channel_id:0,
		images:[],
		info:'',
		page:1,
		news:[],
		notice:[],
		name:'',
		hans:'',
		sfznum:'',
		phone:'',
		tip:''

    };
  },  mounted() {
 this.channel_id = this.$route.query.id;

	this.sandMate();
	this.sandRed();
		
		
		},methods: {
			more(){
				this.page+=1;
				this.sandMate()
			},sandRed(){
			  	this.$axios({
			  	  method: "post",
			  	  url: "service.php?action=users_userInfo",
			  	  data: {},  
			  	}).then((res) => {
			  		let data=res.data;
			  
			  		if(data.code==1){
			  			this.info=data.data;
				
			  		}
			  	 
			  	});
			  
			  }, 
		sandMate(){
			
			this.$axios({
			  method: "post",
			  url: "service.php?action=users_uliushui",
			  data: {page:this.page,type:this.channel_id},  
			}).then((res) => {
				let data=res.data;
		
				if(data.code==1){
							
				
					if(data.data.length==0&&this.page==1){
						this.tip='暂无内容'
						return
					}
					
					if(data.data.length==0&&this.page!=1){
						this.tip='没有更多了'
						return
					}
					this.tip='查看更多'
					this.news=this.news.concat(data.data);
					
					
					
				}
			 
			});
		
		},sandAlt(){
				this.news=[]
				this.page=1
				this.sandMate()
			
			},  confirm(item){
			   	this.$axios({
			   	  method: "post",
			   	  url: "service.php?action=orders_qrshouhuo",
			   	  data: {orderId:item},  
			   	}).then((res) => {
			   		let data=res.data;
			   	
			   		if(data.code==1){
			   			
			   		Toast(data.message);
					
					 this.news=[]
			this.sandMate();
			   		}else{  
			   			Toast(data.message);
			   			      
			   		}
			   	 
			   	});
			   
			   },sandHans(){
			
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_channel",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
			
							data.data.forEach((item, index) => {
								if(this.channel_id==item.id){
									this.title=item.title
									this.hans=item.channels
									if(this.hans.length>0){
										this.channel_id=this.hans[0].id
									}
									this.sandMate();
									return
								}
							})			
					
						
					}
				 
				});
			
			}
		}
};
</script>
