<template>
  <div class="index">
  <Title titleName='秒杀产品' iconLeft="back"/>
  

	<div class="home">
	
	
	
	
		
		<div class="index-product">
		
			<div class="con">
				<ul>
					<li v-for="it in news">
						 <router-link :to="'/index/detail?id='+it.inventoryId">
							
						<div class="tu">
							<img :src="it.img"  />
						</div>
						<div class="txt">
							<div class="bot">{{it.title}}</div>
							<p class="money">￥{{it.price_sale}}</p>
							<p class="has">￥{{it.price_market}}</p>
						</div>
						 </router-link>	
					</li>
				
				</ul>
			<div class="more">
				
				<div class="button" @click="more">{{tip}}</div>
				
			</div>
			</div>
			
		</div>
		
	</div>

	



  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import Bar from '@/components/bar/bar'
export default {
  components: {
     Title,
    CardList,
	Bar
  },
  data() {
    return {
		images:[],
		info:'',
		page:1,
		channel_id:0,
		news:[],
		notice:[],
		name:'',
		hans:'',
		sfznum:'',
		phone:'',
		tip:''

    };
  },  mounted() {
	 
	this.name = window.localStorage.getItem('name')
	this.sfznum = window.localStorage.getItem('sfznum')
	this.phone = window.localStorage.getItem('phone')
	this.sandAlt()
	this.sandHans();
		this.sandMate();
		this.sandCase();
		},methods: {
			 onSearch(val) {
			      Toast(val);
			    },	more(){
				this.page+=1;
				this.sandMate()
			},
		sandMate(){
			this.$axios({
			  method: "post",
			  url: "service.php?action=product_plist",
			  data: {page:this.page,channel_id:this.channel_id,if_miaosha:1},  
			}).then((res) => {
				let data=res.data;
					
				if(data.code==1){
					
					
				
				if(data.data.length==0&&this.page==1){
					this.tip='暂无内容'
					return
				}
				
				if(data.data.length==0&&this.page!=1){
					this.tip='没有更多了'
					return
				}
				this.tip='查看更多'
				this.news=this.news.concat(data.data);
				
					
					
				}
			 
			});
		
		},sandCase(){
				this.$axios({
				  method: "post",
				  url: "/api/noticelist",
				  data: {page:1},  
				}).then((res) => {
					let data=res.data;
					
					if(data.code==200){
						
						this.notice=data.data[0];
						
						
					
					}
				 
				});
			},sandAlt(){
				this.$axios({
				  method: "post",
				  url: "service.php?action=index_index",
				  data: {type:2},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
				
										
						this.images=data.data.banners;
						
						
					}
				 
				});
			
			},sandHans(){
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_channel",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
				
										
						this.hans=data.data;
						
						
					}
				 
				});
			
			}
		}
};
</script>
