<template>
  <div class="index">
    <Title titleName='政策公告' iconLeft="back" />

		<van-tabs v-model="active" color="#2892FF">
		  <van-tab title="通知公告">
			  <div class="news">
			 
			  	<div class="cont" id="content">
			  		<ul>
						<router-link v-for="value in notice" :to="'/news/detailsNo?id='+value.id">
						
			  			<li>
			  			
			  				<div class="txt">
			  					<h4>{{value.title}}</h4>
			  					<p>{{value.cdate}}</p>
			  					<p>{{value.content.substring(0,60)}}</p>
			  				</div>
			  			</li></router-link>
						<div class="h-more" @click="more">{{tip}}</div>
			  	
			  		</ul>
			  	</div>
			  	
			  </div>
			  
		  </van-tab>
		  <van-tab title="行业资讯">
			  <div class="news">
			  			 
			  	<div class="cont" id="content">
			  		<ul>
			  						<router-link v-for="value in news" :to="'/news/details?id='+value.id">
			  						
			  			<li>
			  			
			  				<div class="txt">
			  					<h4>{{value.title}}</h4>
			  					<p>{{value.cdate}}</p>
			  					<p>{{value.content.substring(0,60)}}</p>
			  				</div>
			  			</li></router-link>
			  						<div class="h-more" @click="more2">{{tip2}}</div>
			  	
			  		</ul>
			  	</div>
			  	
			  </div>
			  
			  
		  </van-tab>
		  <van-tab title="文件下载">
			  <div class="news">
			  
			  	<div class="cont">
			  		<ul class="down-fl">
			  						
			  			<li v-for="value in file">
			  			
			  				<div class="txt">
			  					<h4>{{value.title}}</h4>
			  					<p>{{value.cdate}}</p>
			  					
			  				</div>
							
							<div @click="dov(value.files)" class="button"><img src="../../assets/down.png">下载</div>
			  			</li>
			  			
			  		</ul>
			  	</div>
			  	
			  </div>
		  </van-tab>
		 
		</van-tabs>




	
		
		
	 <Bar />
  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";


export default {
  components: {
    Title,
    CardList
  },
  data() {
    return {
		active: 0,
		notice:[],
		news:[],
		page:1,
		file:[],
		page2:1,
		tip:'',
		tip2:''
    };
  },
  mounted() {
	 this.active = parseInt(this.$route.query.id)
	 this.sandCase();
	 this.sandMate();
	 this.sandColl();
  	
  },

  methods: {
    dov(sand) {
      window.open(sand)
    },sandCase(){
	
				this.$axios({
				  method: "post",
				  url: "/api/noticelist",
				  data: {page:this.page},  
				}).then((res) => {
					let data=res.data;
					
					if(data.code==200){
						if(data.data.length==0&&this.page==1){
							this.tip='暂无内容'
							return
						}
						
						if(data.data.length==0&&this.page!=1){
							this.tip='没有更多了'
							return
						}
						this.tip='查看更多'
						this.notice=this.notice.concat(data.data);
						
						
					
					}
				 
				});
			},sandMate(){
	
				this.$axios({
				  method: "post",
				  url: "/api/hynewslist",
				  data: {page:this.page2},  
				}).then((res) => {
					let data=res.data;
					
					if(data.code==200){
						if(data.data.length==0&&this.page2==1){
							this.tip2='暂无内容'
							return
						}
						
						if(data.data.length==0&&this.page2!=1){
							this.tip2='没有更多了'
							return
						}
						this.tip2='查看更多'
						this.news=this.news.concat(data.data);
						
						
					
					}
				 
				});
			},more(){
				this.page+=1;
				this.sandCase()
			},more2(){
				this.page2+=1;
				this.sandMate()
			},sandColl(){
	
				this.$axios({
				  method: "post",
				  url: "/api/filelist",
				  data: {},
				}).then((res) => {
					let data=res.data;
					
					if(data.code==200){
						
						this.file=data.data;
						
						
					
					}
				 
				});
			}
  }
};
</script>
<style lang="scss">

.down-fl{
	li{
		justify-content: space-between;
		.button{
			display: flex;
			justify-content: center;
			align-items: center;
			img{
				width: 38px;
				height: 35px;
			}
		}
	}
}

.h-more{
	font-size: 13px;
	color: #bbbbbb;
	height:70px;
	line-height: 70px;
}

</style>