<template>
  <div class="index">
    <Title titleName='政策公告' iconLeft="back" />
	
		<van-tabs v-model="active" color="#2892FF" @click="sand">
		  <van-tab title="通知公告">
			 
			  
		  </van-tab>
		  <van-tab title="行业资讯">
			  <div class="news">
			  
			  	<div class="cont">
			  					<div class="details">
			  						<div class="title">
			  							
			  							{{file.title}}
			  						</div>
			  						<div class="time">时间：{{file.cdate}}     浏览量 ：{{file.views||0}}</div>
			  						<div class="content" v-html="file.content">
			  					
			  						</div>
			  						<div class="next">
			  							<p v-if="file.preid=='无'">上一条：{{file.pretitle}}</p>
			  							<p v-else>上一条：<router-link :to="'/news/details?id='+file.preid">{{file.pretitle}}</router-link></p>
			  							<p v-if="file.nextid=='无'">上一条：{{file.nexttitle}}</p>
			  							<p v-else>上一条：<router-link :to="'/news/details?id='+file.nextid">{{file.nexttitle}}</router-link></p>
			  						</div>
			  					</div>
			  	</div>
			  	
			  </div>
		  </van-tab>
		  <van-tab title="文件下载"></van-tab>
		 
		</van-tabs>


		
	
  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";

export default {
  components: {
    Title,
    CardList
  },
  data() {
    return {
		active: 1,
		id:'',
		file:''
    
    };
  },watch:{
  '$route':function(item,icon){  
        if(item!=icon){	
            this.id=this.$route.query.id;
			this.sandColl();
			this.sandHall();
          }
  }
},mounted() {
  	this.id = this.$route.query.id
	this.sandColl();
	this.sandHall();
  },methods:{
	  sandColl(){
	  	
	  	this.$axios({
	  	  method: "post",
	  	  url: "/api/hynewsdetail",
	  	  data: {hid:this.id},
	  	}).then((res) => {
	  		let data=res.data;
	  		
	  		if(data.code==200){
	  			
	  			this.file=data.data;
	  			
	  			
	  		
	  		}
	  	 
	  	});
	  },sandHall(){
	  	
	  	this.$axios({
	  	  method: "post",
	  	  url: "/api/addviews",
	  	  data: {id:this.id,type:2},
	  	}).then((res) => {});
	  },sand(name, title) {
     if(name==0){
		this.$router.replace({ path:"/news/list?id=0" });
	 }
	if(name==2){
			this.$router.replace({ path:"/news/list?id=2" });
	}
    }
  }
};
</script>
<style lang="scss">

.details{
	padding-bottom: 20px;
	.title{
		font-size: 36px;
		
	}
	.time{
		color: #9E9CA2;
		height:80px;
		line-height: 80px;
		border-bottom: 1px dotted #d3d3d3;
		margin-bottom: 30px;
	}
	.content{
		text-align: left;
	}
	.next{
		text-align: left;
		margin: 30px 0;
		p{
			height: 50px;
			line-height: 50px;
		}
	}
}
 
</style>