<template>
  <div class="fund">
    <Title titleName='企业员工' />



		
		<div class="news">
			<div class="tit">
				<div class="fund-tit">企业基础信息</div>
			</div>
			<div class="info-fund">
						 <ul>
							 <li><span>企业名称</span><span>{{enterprise.qyname}}</span></li>
						
							 
						 </ul>
			</div>
		
		</div>

		<div class="news">
			<div class="tit">
				<div class="fund-tit">员工列表</div>
			</div>
			<div class="info-fund">
						 <ul>
							 <li v-for="item in file" @click="balance(item)"><span>{{item.name}}</span><span><img class="q-card" src="../../assets/card.png" />{{item.sfznum}}<img class="q-more" src="../../assets/rig.png" /></span></li>
							
							 
						 </ul>
			</div>
		
			
		
			
		</div>
	


  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";

export default {
  components: {
    Title,
    CardList,
  },
  data() {
    return {
		sum:0,
		year:0,
		file:'',
		enterprise:''
    
    };
  },mounted() {
	var obj=new Date();
	this.year=obj.getFullYear();
	let enterprise = JSON.parse(window.localStorage.getItem('enterprise'));
	this.enterprise=enterprise;
	this.sandColl();

  },methods:{
	  sandColl(){
	  
	  	this.$axios({
	  	  method: "post",
	  	  url: "/api/qyygs",
	  	  data: {userid:this.enterprise.id,token:this.enterprise.token,qyname:this.enterprise.qyname},
	  	}).then((res) => {
		
	  		let data=res.data;
	  		
	  		if(data.code==200){
			
				this.file=data.data;
	  	
	  		
	  			
	  		
	  		}
	  	 
	  	});
	  },fund(value){
		  if(value){
			  this.year-=1
		  }else{
			  this.year+=1
			
		  }
		  this.sandColl();
	  },balance(item){
		  localStorage.setItem('userid',this.enterprise.id)
		  localStorage.setItem('token',this.enterprise.token)
		  localStorage.setItem('name',item.name)
		  localStorage.setItem('sfznum',item.sfznum)
		  localStorage.setItem('phone',item.phone)
		  localStorage.setItem('qiye',item.qiye)
		  this.$router.push({ path: "/index/home" });	
		  
	  }
  }
};
</script>
<style lang="scss">
.info-fund li span .q-card{
	width: 33px;
	height: 24px;
}
.info-fund li span .q-more{
	width: 13px;
	height: 22px;
	margin-left: 10px;
}
</style>