<template>
  <div class="index">
 <Title titleName='商家中心' iconLeft="back"/>

	<div class="mem-con">
		<div class="mem-l1">
			<van-row>
				<van-col span='6'>
					<img class="head" :src="user.avatar"/>
				</van-col>
				<van-col span='14'>
					<h3>  {{user.store_name}}</h3>
				
				</van-col>
				<van-col span='4'  class="mem-sim" >
					<router-link to="/user/gift">
					
				
					<p>提现记录></p>
					
					</router-link>
				</van-col>
			</van-row>
		
		</div>
	
		

	</div>
	<div class="brave">
		<div class="win">
			<div class="leave">
				<div class="l1">
					<ul>
						<li>
							<h5>{{user.store_info.freeze_money}}</h5>
							<p>待结算（元）</p>
						</li>
						<li>
							<h5>{{user.store_info.settle_money}}</h5>
							<p>已结算（元）</p>
						</li>
						<li>
							<h5>{{user.store_info.balance}}</h5>
							<p>可提现（元）</p>
						</li>
					</ul>
				</div>
				<div class="l2">
					<div class="lab">提现至</div>
					<div class="cid">设置支付宝账号<img src="../../assets/ip.png"></div>
				</div>
				<div class="l3">
					<div class="nt"><span>￥</span>{{user.store_info.balance}}</div>
					<div class="btn">提现</div>
				</div>
			</div>
			<div class="honor">
				<h4>今日业绩￥0.00元</h4>
			</div>
		</div>
	</div>




  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import { Toast } from 'vant';	
export default {
  components: {
    Title,
    CardList
  },
  data() {
    return {
		title:'',
		channel_id:0,
		images:[],
		info:'',
		user:'',
		page:1,
		news:[],
		notice:[],
		name:'',
		hans:'',
		sfznum:'',
		phone:'',
		tip:''

    };
  },  mounted() {
 this.channel_id = this.$route.query.id;
this.seat()
	this.sandMate();
		
		
		},methods: {
			more(){
				this.page+=1;
				this.sandMate()
			},sandRed(id){
			  	this.$axios({
			  	  method: "post",
			  	  url: "service.php?action=paypal_pay",
			  	  data: {order_id:id},  
			  	}).then((res) => {
			  		let data=res.data;
			  
			  		if(data.code==1){
			  			window.location.href=data.data.link	
				
			  		}
			  	 
			  	});
			  
			  }, 
			  seat(){
			  	this.user = JSON.parse(window.localStorage.getItem('user'));
			  
			  },
		sandMate(){
			
			this.$axios({
			  method: "post",
			  url: "service.php?action=orders_lists",
			  data: {page:this.page,scene:this.channel_id},  
			}).then((res) => {
				let data=res.data;
		
				if(data.code==1){
							
				
					if(data.data.length==0&&this.page==1){
						this.tip='暂无内容'
						return
					}
					
					if(data.data.length==0&&this.page!=1){
						this.tip='没有更多了'
						return
					}
					this.tip='查看更多'
					this.news=this.news.concat(data.data);
					
					
					
				}
			 
			});
		
		},sandAlt(){
				this.news=[]
				this.sandMate()
			
			},  confirm(item){
			   	this.$axios({
			   	  method: "post",
			   	  url: "service.php?action=orders_qrshouhuo",
			   	  data: {orderId:item},  
			   	}).then((res) => {
			   		let data=res.data;
			   	
			   		if(data.code==1){
			   			
			   		Toast(data.message);
					
					 this.news=[]
			this.sandMate();
			   		}else{  
			   			Toast(data.message);
			   			      
			   		}
			   	 
			   	});
			   
			   },sandHans(){
			
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_channel",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
			
							data.data.forEach((item, index) => {
								if(this.channel_id==item.id){
									this.title=item.title
									this.hans=item.channels
									if(this.hans.length>0){
										this.channel_id=this.hans[0].id
									}
									this.sandMate();
									return
								}
							})			
					
						
					}
				 
				});
			
			}
		}
};
</script>
<style lang="scss" scoped>


 .mem-con{
 	 background: url(../../assets/bg.png) no-repeat;
 	 background-size: 100%;
	 height: 200px;
 	 font-size: 24px;
 	 color: #fff;
 	 padding: 30px;
 	 .mem-l1{
 	
 		 padding: 40px 0;
 		 h3{
 			margin-top: 30px;
 			 font-size: 34px;
 			 color: #fff;
 		 }
 		 .head{
 			width: 100px;
			height: 100px;
			border-radius: 100px;
			margin-left: 30px;
 		 }
 		 .set{
 			 width: 42px;
 		 }
 		 .shop{
 			 width: 120px;
 			 height: 36px;
 			 border-radius: 18px;
 			 color: #7D7D7D;
 			 background: #fff;
 			 text-align: center;
 			 font-size: 24px;
 			 margin-top: 15px;
 		 }
 		 .mem-sim{
 			 text-align: center;
 			 margin-top: 30px;
 			
 			 p{
 				 color: #fff;
 			 }
 		 }
 		 
 	 }
 	
 }
.brave{
	position: fixed;
	width: 100%;
	top:290px;
	.win{
		width: 670px;
		margin: 0 auto;
		.leave{
			border-radius: 20px;
			background: #ffffff;
			padding: 0 50px;
			height: 440px;
			.l1{
				border-bottom: 1px solid #DDDDDD;
				padding: 40px 0;
				ul{
					display: flex;
					align-items: center;
					justify-content: space-between;
					li{
						text-align: center;
						h5{
							font-size: 34px;
							margin-bottom: 10px;
						}
						p{
							font-size: 24px;
							color: #999999;
						}
					}
				}
			}
			.l2{
				display: flex;
				justify-content: space-between;
				align-items: center;
				border-bottom: 1px solid #DDDDDD;
				height: 110px;
				.cid{
					display: flex;
					align-items: center;
					img{
						width: 30px;
						height: 30px;
					}
				}
			}
			.l3{
				display: flex;
				justify-content: space-between;
				margin-top: 40px;
				.nt{
					font-size: 72px;
					color: #DDDDDD;
					display: flex;
					span{
						color: #222222;
						font-size: 54px;
					}
				}
				.btn{
					width: 160px;
					height: 80px;
					background: #FF5E00;
					border-radius: 40px;
					color: #FFFFFF;
					font-size: 28px;
					text-align: center;
					line-height: 80px;
				}
			}
		}
		.honor{
			h4{
				color: #222222;
				font-size: 28px;
				font-weight: normal;
				margin-top: 50px;
			}
		}
	}
}
</style>