<template>
  <div class="login">
   <Title titleName='收货地址' iconLeft="back"/>


	<div class="order">

<div class="pro-l2">
	
	<div class="can" v-for="item in news" >
		<div class="d1">
		<div @click="check(item)">
			<p><b>{{item.name}}  {{item.phone}}</b></p>
			<p>{{item.address.substring(0,30)}}</p>
			</div>
		<div class="pl"><router-link :to="'/user/addAdr?id='+item.id">编辑</router-link></div>
		</div>
		<!-- <div class="d2">
			<div class="pr">
				<div class="img"><img v-if="item.if_default==1" src="../../assets/k2.png"><img v-else @click="def(item.id)" src="../../assets/k1.png"></div>設為默認
			</div>
			<div>
				<span @click="del(item.id)" >删除</span> <span><router-link :to="'/user/addAdr?id='+item.id">修改</router-link></span>
			</div>
		</div> -->
	</div>

</div>

	</div>

	<div class="add-button"><router-link to="/user/addAdr"><van-button color="#FF5E00" block>添加新地址</van-button></router-link></div>
	
	
	
  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import { Toast } from 'vant';	
export default {
  components: {
    Title,
    CardList

  },
  data() {
    return {
		active: 1,
		id:'',
		file:'',
		page:1,
		news:'',
		pagenum:100,
		ruleForm:{
			email:'',
			password:''
		}
    
    };
  },mounted() {
	  this.sandMate();
  	this.id = this.$route.query.id

  },methods:{
	  sandMate(){
	  	this.$axios({
	  	  method: "post",
	  	  url: "service.php?action=userAddress_lists",
	  	  data: {page:this.page,pagenum:this.pagenum},  
	  	}).then((res) => {
	  		let data=res.data;
	  
	  		if(data.code==1){
	  	
	  							
	  			this.news=data.data;
	  			
	  			
	  		}
	  	 
	  	});
	  
	  },
	def(id){
	  	this.$axios({
	  	  method: "post",
	  	  url: "service.php?action=userAddress_setDefault",
	  	  data: {id:id},  
	  	}).then((res) => {
	  		let data=res.data;
	  
	  		if(data.code==1){
				this.sandMate();
	  			Toast(data.message);
	  			
	  		}else{
				Toast(data.message);
			}
	  	 
	  	});
	  
	  },
	  del(id){
	    	this.$axios({
	    	  method: "post",
	    	  url: "service.php?action=userAddress_delete",
	    	  data: {id:id},  
	    	}).then((res) => {
	    		let data=res.data;
	    
	    		if(data.code==1){
	  			this.sandMate();
	    			Toast(data.message);
	    			
	    		}else{
	  			Toast(data.message);
	  		}
	    	 
	    	});
	    
	    },check(item){
	    	localStorage.setItem("address", JSON.stringify(item));
			this.$router.back()
		
	    },
	loginForm(){
				
				if(this.ruleForm.phone == ''){
				
					Toast('请输入邮箱');
					return
				}
				if(this.ruleForm.password == ''){
					Toast('请输入密码');
					return
				}
				
				
			this.$axios({
			      method: "post",
			      url: "service.php?action=users_emailLogin",
			      data: this.ruleForm,  
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==1){
									
						localStorage.setItem("user", JSON.stringify(data.data));
						Toast(data.message);
					this.$router.push({ path: "/" });	
			    		
			    	}else{  
									Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},checkPhone(phone){ 
				if(!(/^1[3456789]\d{9}$/.test(phone))){ 
					Toast('请填写正确的手机号');
					
					return false; 
				} else{
					return true
				}
			}
  }
};
</script>
<style lang="scss" scoped>
.order{
	font-size: 28px;
	padding-bottom: 200px;
}
.pro-l2{
	background: #FFFFFF;
	padding:0 20px;
	border-top: 1px solid #DDDDDD;
	.can{
		
		border-bottom: 1px solid #DDDDDD;
		.d1{
			display: flex;
			justify-content: space-between;
			height:150px;
			align-items: center;
			border-bottom: 1px dotted #DDDDDD;
			line-height: 50px;
			.pl{
				border-left: 1px solid #DDDDDD;
				padding: 10px 20px;
			}
		}
		.d2{
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 15px 0;
			span{
				margin-left: 20px;
			}
			.pr{
				display: flex;
				align-items: center;
				.img{
					margin-top: 10px;
					margin-right: 20px;
					img{
						width: 30px;
					}
				}
			
			}
		}
		
	}
	.can1{
		height: 100px;
		line-height: 100px;
	}

}
.add-button{
	position: fixed;
	width: 100%;
	bottom: 0;
}
 .pro-tit{
	 background: #FFFFFF;
	 text-align: center;
	 line-height: 40px;
	 padding: 20px 0;
	 h3{
		 font-size: 36px;
	 }
	 p{
		 font-size: 24px;
	 }
 }
</style>