<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import { Toast } from 'vant';	
export default {
  components: {
    Title,
    CardList

  },
  data() {
    return {
		reg: {
			id:'',
			market_left:'',
			market_right:'',
			contain_cinema:'',
			un_contain_cinema:'',
			contain_movie:'',
			un_contain_movie:'',
			contain_hall:'',
			un_contain_hall:'',
			lovers_seat:1,
			accept_seats:1,
			seats_number:0,
			offer_type:1,
			offer_value:'',
			state:1
			         
		      },
    
    };
  },mounted() {
	  
	if(this.$route.query.id){
		this.reg.id = this.$route.query.id
		this.funder()
	}
	  
  	
	this.sandMate()
	this.sandCase()
  },methods:{
	   onConfirm(value) {
		  
	       this.reg.areaName = value[0].name+'-'+value[1].name+'-'+value[2].name;
		   this.reg.area_id=value[2].code
		   this.reg.province_name=value[0].name
		   this.reg.city_name=value[1].name
		   this.reg.area_name=value[2].name
	       this.showPicker = false;
	      },
	  submitForm() {
	  				
	  			
	          this.$axios({
	            method: "post",
	            url: "stores/offer_rule",
	            data: this.reg,  
	          }).then((res) => {
	          	let data=res.data;
	          			
	          	if(data.code==200){
	  						
	  			  Toast(data.message);
				  this.$router.back()
	    
	          		
	          	}else{  
	  				Toast(data.message);
	  						      
	  					}
	           
	          });
	  				 
	  		
	       
	       },funder(){
			   this.$axios.get('stores/rule_list',{params:{
				   rule_id:this.reg.id
			   }}).then((res)=>{
				   let data=res.data;
				   if(data.code==200){
					   this.reg=data.data
					   
				   }
			   })
			   

			  				 
	}, sandCase(){
			  	this.$axios({
			  	  method: "post",
			  	  url: "service.php?action=index_ssqlist",
			  	  data: {},  
			  	}).then((res) => {
			  		let data=res.data;
			  	
			  		if(data.code==1){
			  			//this.columns=data.data.data;
			  		}
			  	 
			  	});
			  
			  },sandMate(){
				this.$axios({
				  method: "post",
				  url: "service.php?action=userAddress_country",
				  data: {},  
				}).then((res) => {
					let data=res.data;
				
					if(data.code==1){
						this.news=data.data;
					}
				 
				});
			
			},sandCate(){
				this.$axios({
				  method: "post",
				  url: "service.php?action=userAddress_detail",
				  data: {id:this.reg.id},  
				}).then((res) => {
					let data=res.data;
				
					if(data.code==1){
						this.reg=data.data;
					}
				 
				});
			
			},cell(date) {
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();

      month = month < 10 ? "0" + month : month;
      day = day < 10 ? "0" + day : day;

      return year + "-" + month + "-" + day;
    },
	loginForm(){
				let that = this;
				let phone_num = this.loginData.phone;
				if(this.loginData.phone == ''){
				
					Toast('请输入手机号');
					return
				}else{
					if(!this.checkPhone(phone_num)){
                    return false;
                }
				}
				if(this.loginData.password == ''){
					Toast('请输入密码');
					return
				}
				let data = this.loginData;
				
				
				this.$axios({
				  method: "post",
				  url: "/wx/qylogin",
				  data:data,
				}).then((res) => {
			
					let data=res.data;
					if(data.code==200){
						Toast(data.message);
						localStorage.setItem("enterprise", JSON.stringify(data.data));
						this.$router.push({ path: "/loger" });	
						
					}else{
						Toast(data.message);
						}
				 
				});
				
				
				
				
				
				// loginFun(data).then(res =>{
				// 	console.log(res);
				// 	if(res.code == 0){
				// 		Toast('登录成功');
				// 		localStorage.setItem("userInfo", JSON.stringify(res.data.user));
				// 		that.$router.replace({ path: "/index" });
				// 	}else{
				// 		that.$toast(res.message);
				// 	}
				// })
			},checkPhone(phone){ 
				if(!(/^1[3456789]\d{9}$/.test(phone))){ 
					Toast('请填写正确的手机号');
					
					return false; 
				} else{
					return true
				}
			}
  }
};
</script>
