<template>
  <div class="login">
  <Title titleName='写评价' iconLeft="back"/>


	<div class="order">



<div class="pro-l2">


	<van-row class="can2" gutter='20' v-for="item in news.products" v-if='id==item.id'>
		<van-col span="8"><img :src="item.image"></van-col>
		<van-col span="16">
			<p>{{item.title}}</p>
			<p>规格：{{item.key_vals}}</p>
			<p><span>x{{item.num}}</span><span>￥{{item.price_sale}}</span></p>
		</van-col>
	</van-row>




</div>

  <div class="content-box">
	
      <div class="content">
		   <div class="h-tit">商品评价</div>
        <van-form @submit="onSubmit_comment">
			
          <van-field name="score">
            <template #input>
              <van-rate
                v-model="star"
                @change="onChange_comment_rate"
              
                color="#F5A628"
                void-icon="star"
                void-color="#eee"
              />
            </template>
          </van-field>

          <van-field
            v-model="content"
            name="content"
            border
            autofocus
            rows="6"
            type="textarea"
            maxlength="200"
            placeholder="写出您的感受，可以帮助更多小伙伴哦~"
            
          />

          <van-field name="uploader_FY" >
            <template #input>
              <van-uploader
                v-model="uploader_FY"
				:after-read="afterRead"
                :before-delete="delUploadImg_FY"
                accept="image/*"
                multiple
              />
            </template>
          </van-field>

          <div style="margin: 16px;">
            <van-button block color="#FF5E00" native-type="submit">提交</van-button>
          </div>
        </van-form>
      </div>
    </div>

<!-- <div class="pro-l3">
	<van-button color="#FF5E00" v-if="news.status=-5" @click="sandRed" block>去付款</van-button>
</div> -->


	</div>

<!-- 	<div class="adr" v-if="news.status=-5">
	<van-button type="default" v-if="news.status=-5" @click="cancel" block>取消订单</van-button>
			<van-button color="#FF5E00" v-if="news.status=-5" @click="sandRed" block>去付款</van-button>
	</div> -->

	
  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import { Toast } from 'vant';	
export default {
  components: {
    Title,
    CardList

  },
  data() {
    return {
	page:1,
	pagenum:100,
	moren:false,
	activeName: '0',
	sun:'',
	active:1,
	id:'',
	total:'',
	fin:0,
	rt:'',
	star: 5, 
	content: "", 
	uploader_FY: [], 
	uploaderCompressed_FY: [] ,
	promiseArr:[],
	news:'',
	   ruleForm: {
	   	first_name:'',
	   	last_name:'',
	 
	   	phone:'',
	   	nickname:'',
	   	country:'',
	   	identity_id:'',
	   	address_line:'',
	   	province:'',
	   	city:'',
	   	postcode:'',
		moren: 0,
	  
	       
	         },
	      
    
    };
  },mounted() {
  	this.id = this.$route.query.id
	this.rt = this.$route.query.rt
	this.sandMate()

  },methods:{

	sandMate(){
		this.$axios({
		  method: "post",
		  url: "service.php?action=orders_detail",
		  data: {id:this.rt},  
		}).then((res) => {
			let data=res.data;
	
			if(data.code==1){
		
								
				this.news=data.data;
					
							
							this.active=data.data.status
				this.total=data.count
				
			}
		 
		});
	
	},  afterRead(file) {
		
		 this.promiseArr=[]
			
		this.uploader_FY.forEach(v => {
		
		 this.$axios({
		    	  method: "post",
		    	  url: "service.php?action=index_upfile",
		    	  data: {img:v.content},  
		    	}).then((res) => {
		    		let data=res.data;
		    	
		    		if(data.code==1){
		    			
		    		Toast(data.message);
		 		 this.promiseArr.push(data.image);
		    		}else{  
		    			Toast(data.message);
		    			      
		    		}
		    	 
		    	});
		 
		 
		 
		});
		
		
	
	    },
	onSubmit_comment(values) {
	 
	  if (!this.star) {
	    Toast('请选择评分');
	    return;
	  }
	  if (!this.content) {
	     Toast('请输入评价');
	    return;
	  }
	  
	
	   this.$axios({
	     method: "post",
	     url: "service.php?action=orders_comment",
	     data: {order_id:this.id,inventoryId:this.rt,star:this.star,content:this.content,uploadedfile1:this.promiseArr[0],uploadedfile2:this.promiseArr[1],uploadedfile3:this.promiseArr[2]},  
	   }).then((res) => {
	   	let data=res.data;
	   			
	   	if(data.code==1){
	  		Toast(data.message);
	
	  	this.$router.back()
	   	}else{  
	  	Toast(data.message);
	  		      
	  	}
	    
	   });
	   
	

	
	},

	loginForm(){
				
				if(this.ruleForm.phone == ''){
				
					Toast('请输入邮箱');
					return
				}
				if(this.ruleForm.password == ''){
					Toast('请输入密码');
					return
				}
				
				
			this.$axios({
			      method: "post",
			      url: "service.php?action=users_emailLogin",
			      data: this.ruleForm,  
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==1){
									
						localStorage.setItem("user", JSON.stringify(data.data));
						Toast(data.message);
					this.$router.push({ path: "/" });	
			    		
			    	}else{  
									Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			}, 
			pay() {
				
				
				if (!this.is_add) {
					Toast('收货地址不能为空');
					return false;
				}
				
				this.$axios({
				  method: "post",
				  url: "service.php?action=orders_create",
				  data: {address_id:this.address.id,product_info:JSON.stringify(this.product_info),korean_code:this.params_info.korean_code,remark:this.params_info.remark,yhq_id:this.params_info.yhq_id},  
				}).then((res) => {
					let data=res.data;
							  
				if(data.code==1){
										
					Toast(data.message);					
							
				if(this.is_use_jifen){
												 this.$axios({
												   method: "post",
												   url: "service.php?action=orders_jifenPay",
												   data: {order_id:data.order_id,jifen:this.info.jifen},  
												 }).then((res) => {
												 
												 
												 	if(data.code==1){
												 		
												 	if(this.pay_type==1){
														this.sandRed(data.order_id)
													}  
												 			
												 	}
												  
												 });
				}else{
												if(this.pay_type==1){
													this.sandRed(data.order_id)
												} 
				}
				    		
				    	}else{  
										Toast(data.message);	
										      
									}
				 
				});
				
				
			// 	API.getJson('orders_create', this.params_info).then((res) => {
			// 		console.log(res);
			// 		if (res.code == 1) {
			// 			let id = res.order_id
			// 			this.order_id = id
			// 			if (this.is_use_jifen) {
			// 				this.jifen_pay(id)
			// 			}
			// 			if (this.pay_type == 1) {
			// 				//微信支付
			// 				this.$m.loading(6000)
			// 				this.wechat_pay(id)
			// 			} else {
			// 				this.is_show_pay = true
			// 			}
			
			// 		} else {
			// 			msg(res.message)
			// 		}
			// 	})
			
			},
			
			onConfirm(value) {
     this.pay_text=value.text
	 this.pay_type =value.value
	 this.showPicker=false
    },	get_money(val1, val2, val3) {
				if(this.is_use_jifen){
					val2 = val2 || 0
					if (Number(val1) > (Number(val2)+Number(val3))) {
						return (Number(val1) - Number(val2)-Number(val3)-Number(this.info.yunfei)).toFixed(2)
					}
					return 0;
				}else{
					val2 = val2 || 0
					if (Number(val1) > Number(val2)) {
						return (Number(val1) - Number(val2)-Number(this.info.yunfei)).toFixed(2)
					}
					return 0;
				}
				
			},checkPhone(phone){ 
				if(!(/^1[3456789]\d{9}$/.test(phone))){ 
					Toast('请填写正确的手机号');
					
					return false; 
				} else{
					return true
				}
			}, 	user_coupon(item) {
				this.coupon_info = item
				this.params_info.yhq_id = item.id
				this.is_show_coupon = false
			},  cancel(){
			  	this.$axios({
			  	  method: "post",
			  	  url: "service.php?action=orders_qxOrder",
			  	  data: {order_id:this.id},  
			  	}).then((res) => {
			  		let data=res.data;
			  
			  		if(data.code==1){
			  			
			  		Toast(data.message);	
			  			this.sandMate();		
			  		}else{  
			  		Toast(data.message);	
			  			      
			  		}
			  	 
			  	});
			  
			  },sandCase(){
			  	this.$axios({
			  	  method: "post",
			  	  url: "service.php?action=orders_getPayInfo",
			  	  data: {product_info:JSON.stringify(this.product_info)},  
			  	}).then((res) => {
			  		let data=res.data;
			  
			  		if(data.code==1){
			  					
			  			this.info=data.data;
						
			  		
			  		}
			  	 
			  	});
			  
			  },	use_jifen() {
			
				this.is_use_jifen = !this.is_use_jifen
			},	   sandRed(){
			  	this.$axios({
			  	  method: "post",
			  	  url: "service.php?action=paypal_pay",
			  	  data: {order_id:this.id},  
			  	}).then((res) => {
			  		let data=res.data;
			  
			  		if(data.code==1){
			  			window.location.href=data.data.link	
				
			  		}
			  	 
			  	});
			  
			  },
  }
};
</script>
<style lang="scss" scoped>
.order{
	font-size: 28px;
	padding-bottom: 100px;
}
.pro-l1{
	background: #FFFFFF;
	padding:0 20px;
	margin-bottom: 30px;
	.can1{
		height: 100px;
		line-height: 100px;
	
	}
	.can2{
		line-height: 40px;
		padding-bottom: 20px;
	}
}
.pro-l2{
	background: #FFFFFF;
	padding:0 20px;
	
	margin-bottom: 30px;
	.can{
		padding: 20px 0;
		.rit{
			text-align: right;
			
			font-size: 30px;
			font-family: PingFang SC;
			font-weight: 800;
			color: #E42626;
			

		}
		p{
			line-height: 50px;
		}
		
	}
	.can1{
		height: 100px;
		line-height: 100px;
		border-bottom: 1px solid #DDDDDD;
	}
	.can2{
		padding: 20px 0;
		img{
			width: 100%;
		}
		p{
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom:5px;
		}
	}
	.coupon{
		li{
			padding: 20px;
			border-bottom: 1px solid #DDDDDD;
			display: flex;
			justify-content: space-between;
		}
	}

}
.pro-l3{
	padding: 0 20px;
}
	.can4{
		margin-top: 20px;
		padding: 0 20px;
		background: #FFFFFF;
		border-bottom: 1px solid #DDDDDD;
		display: flex;
		justify-content: space-between;
		height: 100px;
		align-items: center;
		.pl{
			display: flex;
			align-items: center;
			img{
				width: 30px;
				height: 30px;
			}
			input{
				border: none;
				text-align: right;
				width: 600px;
			}
		}
	}
 .pro-tit{
	 background: #FFFFFF;
	 text-align: center;
	 line-height: 40px;
	 padding: 20px 0;
	 h3{
		 font-size: 36px;
	 }
	 p{
		 font-size: 24px;
	 }
 }
 .adr{
 	display: flex;
 	align-items: center;
	justify-content: space-between;
 	position: fixed;
 	bottom: 0;
 	width: 720px;
 	height: 68px;
 	background: #FFFFFF;
 	font-size: 28px;
 	box-shadow: 0px -4px 10px rgba(0, 0, 0, 0.06);
	padding: 20px 0;
	b{
		font-size: 36px;
	}
	button{
		margin:0 10px;
	}
 }
 .content-box{
	 .h-tit{
		 background: #ffffff;
		padding: 30px;
		 font-size: 28px;
		 font-family: PingFang SC;
		 font-weight: 400;
		 color: #333333;
		

	 }
 }
</style>