<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import { Toast } from 'vant';	
export default {
  components: {
    Title,
    CardList

  },
  data() {
    return {
		active: 1,
		id:'',
		news:'',
		file:'',
		 showPicker:false,
		 show:false,
		 minDate: new Date(1900, 0, 1),
		reg: {
			first_name:'',
			last_name:'',
			email:'',
			phone:'',
			nickname:'',
			countory:'',
			identity_id:'',
			line_account:'',
			province:'',
			city:'',
			postcode:'',
			birthday:'',
			instagram:'',
			facebook_id:''
		    
		      },
    
    };
  },mounted() {
  	this.id = this.$route.query.id
	this.sandMate()
	this.sandCate()
  },methods:{
	  
	  submitForm() {
	  				
	  				
	  			      let valid=false
					  if(this.reg.first_name&&this.reg.last_name&&this.reg.email&&this.reg.phone&&this.reg.province&&this.reg.city&&this.reg.postcode){
					
						 valid=true 
					  }else{
						  Toast('请将信息填写完整');
						  
					  }
	    
	           if (valid) {
	          this.$axios({
	            method: "post",
	            url: "service.php?action=users_editInfo",
	            data: this.reg,  
	          }).then((res) => {
	          	let data=res.data;
	          			
	          	if(data.code==1){
	  						
	  			  Toast(data.message);
	       this.$router.push({ path: "/user/user" });	
	          		
	          	}else{  
	  						  Toast(data.message);
	  						      
	  					}
	           
	          });
	  				 
	  				 
	           } else {
	            
	             return false;
	           }
	       
	       },sign(){
		window.localStorage.clear()
		this.$router.push({ path: "/logins" });	
	},sandMate(){
				this.$axios({
				  method: "post",
				  url: "service.php?action=userAddress_country",
				  data: {},  
				}).then((res) => {
					let data=res.data;
				
					if(data.code==1){
						this.news=data.data;
					}
				 
				});
			
			}, sandCate(){
			  	this.$axios({
			  	  method: "post",
			  	  url: "service.php?action=users_userInfo",
			  	  data: {},  
			  	}).then((res) => {
			  		let data=res.data;
			  
			  		if(data.code==1){
			  	
			  							
			  			this.reg=data.data;
			  			
			  			
			  		}
			  	 
			  	});
			  
			  }, onConfirm(value) {
     this.reg.countory=value.DESCRIPTION
	 this.showPicker=false
    },cell(date) {
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();

      month = month < 10 ? "0" + month : month;
      day = day < 10 ? "0" + day : day;

      return year + "-" + month + "-" + day;
    }, onConfirms(value) {
      this.reg.birthday = this.cell(value);
   
      this.show= false;
    },
	loginForm(){
				let that = this;
				let phone_num = this.loginData.phone;
				if(this.loginData.phone == ''){
				
					Toast('请输入手机号');
					return
				}else{
					if(!this.checkPhone(phone_num)){
                    return false;
                }
				}
				if(this.loginData.password == ''){
					Toast('请输入密码');
					return
				}
				let data = this.loginData;
				
				
				this.$axios({
				  method: "post",
				  url: "/wx/qylogin",
				  data:data,
				}).then((res) => {
			
					let data=res.data;
					if(data.code==200){
						Toast(data.message);
						localStorage.setItem("enterprise", JSON.stringify(data.data));
						this.$router.push({ path: "/loger" });	
						
					}else{
						Toast(data.message);
						}
				 
				});
				
				
				
				
				
				// loginFun(data).then(res =>{
				// 	console.log(res);
				// 	if(res.code == 0){
				// 		Toast('登录成功');
				// 		localStorage.setItem("userInfo", JSON.stringify(res.data.user));
				// 		that.$router.replace({ path: "/index" });
				// 	}else{
				// 		that.$toast(res.message);
				// 	}
				// })
			},checkPhone(phone){ 
				if(!(/^1[3456789]\d{9}$/.test(phone))){ 
					Toast('请填写正确的手机号');
					
					return false; 
				} else{
					return true
				}
			}
  }
};
</script>
