<template>
  <div class="index">
    <Title />
	<div class="pro-tit">
		<h3>我的收藏</h3>
	<p>MY COLLECTION</p>
	</div>




	<div class="index-product">
		
		<div class="con">
			<ul>
				<li>
			
				<van-row class="can2" gutter='20' v-for="item in news">
					<van-col span="8"><img :src="item.img"></van-col>
					<van-col span="14">
						<p>{{item.title}}</p>
						<p><span>￥{{item.price_sale}}</span></p>
					</van-col>
					<van-col span="2"><img @click="collect(item)"  src="../../assets/likes.png"></van-col>
				</van-row>
		
				
				</li>
			
			</ul>
			<div class="more">
				
				<div class="button" @click="more">{{tip}}</div>
				
			</div>
		</div>
		
	</div>



  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import { Toast } from 'vant';	
export default {
  components: {
    Title,
    CardList
  },
  data() {
    return {
		title:'',
		channel_id:0,
		images:[],
		info:'',
		page:1,
		news:[],
		notice:[],
		name:'',
		hans:'',
		sfznum:'',
		phone:'',
		tip:''

    };
  },  mounted() {
 this.channel_id = this.$route.query.id;

	this.sandMate();
		
		
		},methods: {
			more(){
				this.page+=1;
				this.sandMate()
			},sandRed(id){
			  	this.$axios({
			  	  method: "post",
			  	  url: "service.php?action=paypal_pay",
			  	  data: {order_id:id},  
			  	}).then((res) => {
			  		let data=res.data;
			  
			  		if(data.code==1){
			  			window.location.href=data.data.link	
				
			  		}
			  	 
			  	});
			  
			  }, 
		sandMate(){
			
			this.$axios({
			  method: "post",
			  url: "service.php?action=product_collectList",
			  data: {page:this.page},  
			}).then((res) => {
				let data=res.data;
		
				if(data.code==1){
							
				
					if(data.data.list.length==0&&this.page==1){
						this.tip='暂无内容'
						return
					}
					
					if(data.data.list.length==0&&this.page!=1){
						this.tip='没有更多了'
						return
					}
					this.tip='查看更多'
					this.news=this.news.concat(data.data.list);
					
					
					
				}
			 
			});
		
		},	collect(it){
			
						
							this.$axios({
							  method: "post",
							  url: "service.php?action=product_collect",
							  data: {inventoryId:it.inventoryId,collect_type:1},  
							}).then((res) => {
								let data=res.data;
								
								if(data.code==1){
									Toast(data.message);
									this.news=[]
								this.sandMate()
								
								}else{
									Toast(data.message);
								}
							 
							});
						},sandAlt(){
				this.news=[]
				this.sandMate()
			
			},sandHans(){
			
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_channel",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
			
							data.data.forEach((item, index) => {
								if(this.channel_id==item.id){
									this.title=item.title
									this.hans=item.channels
									if(this.hans.length>0){
										this.channel_id=this.hans[0].id
									}
									this.sandMate();
									return
								}
							})			
					
						
					}
				 
				});
			
			}
		}
};
</script>
