import Vue from 'vue'
import App from './App.vue'
import router from './router/router'
import 'lib-flexible'
import Vant from 'vant';
import 'vant/lib/index.css';
import axios from 'axios'
import qs from 'qs';
import VueClipboard from 'vue-clipboard2'
import waterfall from 'vue-waterfall2'
Vue.use(waterfall)

Vue.prototype.$axios = axios
Vue.use(VueClipboard)

axios.defaults.baseURL = 'https://ticket.93.zhishangez.cn/api/';

axios.interceptors.request.use(function (config) {
let user=''
let user_id='' 
let  token='' 
// let  token='5605ec7d472c1ab82c1b73e336eebe20' 
//token='a5451fb53de70876717091c23a37ac60'



 
 
 if(window.localStorage.getItem('user')!= "undefined"&&window.localStorage.hasOwnProperty('user')){
   user = JSON.parse(window.localStorage.getItem('user'));
 
   user_id = user.userid
  token = user.token
 
 }

    if(config.method === 'post') {
        let data = qs.parse(config.data)

        config.data = qs.stringify({
            token:token,
			...data
        })
    } else if(config.method === 'get') {
        config.params = {
            token:token,
			...config.params
        }
    }
    return config;
  }, function (error) {
    return Promise.reject(error);
  })



//axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
// axios.defaults.headers.common['userid'] = 1;
// axios.defaults.headers.common['token'] = 'jaychou';



Vue.use(Vant);

Vue.config.productionTip = false

new Vue({
    render: h => h(App),
    router
}).$mount('#app')