<template>
  <div class="index">
    <Title />
	
	<div class="det">
		<h3>{{data.title}}</h3>
		<div class="info">
			{{data.dtTime}} 作者：{{data.author}}
		</div>
		<div v-html="data.content"></div>
	</div>



	

  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";

export default {
  components: {
    Title,
    CardList
  },
  data() {
    return {
		data:'',
		id:'',
		images:[],
		info:'',
		news:[],
		notice:[],
		name:'',
		hans:'',
		sfznum:'',
		phone:''

    };
  },  mounted() {
 this.id = this.$route.query.id;
	
		this.sandMate();
		this.sandCase();
		},methods: {
			
		sandMate(){
		
		this.$axios({
		  method: "post",
		  url: "service.php?action=index_newsDetail",
		  data: {id:this.id},  
		}).then((res) => {
			let data=res.data;
					
			if(data.code==1){
					
				this.data=data.data;
			
				let content = this.data.content
				const regex = new RegExp('<img', 'gi')
				this.data.content = content.replace(regex, `<img style="max-width: 100%; height: auto"`)
				const reges = new RegExp('type="video/x-ms-asf-plugin" width="550" height="400" autostart="false" loop="true" />', 'gi')
				this.data.content=this.data.content.replace(/\<embed/gi, '<video width="100%" controls controlslist="nodownload"><source')
				this.data.content=this.data.content.replace(reges, 'type="video/mp4"></video>')	
			}
		 
		});
		
		
		
		},sandCase(){
				this.$axios({
				  method: "post",
				  url: "/api/noticelist",
				  data: {page:1},  
				}).then((res) => {
					let data=res.data;
					
					if(data.code==200){
						
						this.notice=data.data[0];
						
						
					
					}
				 
				});
			},sandAlt(){
				this.$axios({
				  method: "post",
				  url: "service.php?action=index_index",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
				
										
						this.images=data.data.banners;
						
						
					}
				 
				});
			
			},sandHans(){
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_channelData",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
				
										
						this.hans=data.data;
						
						
					}
				 
				});
			
			}
		}
};
</script>
<style lang="scss" scoped>
.det{
	background: #FFFFFF;
	padding: 20px;
	font-size: 26px;
	color: #999999;
	h3{
	
		font-size: 36px;
		font-family: PingFang SC;
		font-weight: 400;
		color: #000000;
		

	}
	.info{
		font-size: 20px;
		margin: 20px 0;
	}
}
</style>