					<div class="give">送{{item.zeng}}元</div>
				</div>
				<div class="rt">
					<img v-if="item.id==gzid" src="../../assets/cand.png" />
					<img v-else src="../../assets/cand1.png" />
				</div>
			</li>
		
		</ul>
		<div class="yao">
			<div class="ct">
			<p>邀请码<span> (请输入大夫提供的邀请码)</span></p>
