<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import { Toast } from 'vant';	
export default {
  components: {
    Title,
    CardList
  },
  data() {
    return {
		title:'',
		channel_id:0,
		images:[],
		info:'',
		page:1,
		gzid:'',
		biaohao:'00',
		news:[],
		notice:[],
		name:'',
		money:'',
		hans:'',
		sfznum:'',
		phone:'',
		tip:''

    };
  },  mounted() {
 this.channel_id = this.$route.query.id;

	this.sandMate();
	this.sandRed();
		
		
		},methods: {
			more(){
				this.page+=1;
				this.sandMate()
			},sandRed(){
			  	this.$axios({
			  	  method: "post",
			  	  url: "service.php?action=users_userInfo",
			  	  data: {},  
			  	}).then((res) => {
			  		let data=res.data;
			  
			  		if(data.code==1){
			  			this.info=data.data;
				
			  		}
			  	 
			  	});
			  
			  }, 
		sandMate(){
			
			this.$axios({
			  method: "post",
			  url: "service.php?action=money_investActivity",
			  data: {},  
			}).then((res) => {
				let data=res.data;
						  
				if(data.code==1){
			
									
					this.news=data.data;
				
					
				}
			 
			});
		
		},  sandCase(){
				  if(this.money==''){
					  Toast('请选择充值金额');
					
					  return
				  }
			 this.$axios({
			   method: "post",
			   url: "service.php?action=money_aliwapchongzhi",
			   data: {money:this.money,gzid:this.gzid,biaohao:this.biaohao},  
			 }).then((res) => {
			 	let data=res.data;
			 			  
			 	if(data.code==1){
			 				
			 		this.code=data.data
			 		const div = document.createElement('div')
			 		  div.innerHTML = this.code
			 		       document.body.appendChild(div)
			 		       document.forms[0].submit()
			 	
			 	}else{
						this.$message.error(data.message);
				}
			  
			 });
			  
			  },sandAlt(){
				this.news=[]
				this.page=1
				this.sandMate()
			
			},  confirm(item){
			   	this.$axios({
			   	  method: "post",
			   	  url: "service.php?action=orders_qrshouhuo",
			   	  data: {orderId:item},  
			   	}).then((res) => {
			   		let data=res.data;
			   	
			   		if(data.code==1){
			   			
			   		Toast(data.message);
					
					 this.news=[]
			this.sandMate();
			   		}else{  
			   			Toast(data.message);
			   			      
			   		}
			   	 
			   	});
			   
			   },simple(item){
			this.gzid=item.id
			  this.money=item.man
			  },sandHans(){
			
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_channel",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
			
							data.data.forEach((item, index) => {
								if(this.channel_id==item.id){
									this.title=item.title
									this.hans=item.channels
									if(this.hans.length>0){
										this.channel_id=this.hans[0].id
									}
									this.sandMate();
									return
								}
							})			
					
						
					}
				 
				});
			
			}
		}
};
</script>
