<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import { Toast } from 'vant';	
export default {
  components: {
    Title,
    CardList
  },
  data() {
    return {
		title:'',
		channel_id:0,
		images:[],
		info:'',
		page:1,
		news:[],
		notice:[],
		name:'',
		hans:'',
		sfznum:'',
		phone:'',
		tip:''

    };
  },  mounted() {
 
	this.sandMate();
	this.sandRed();
		
		
		},methods: {
			more(){
				this.page+=1;
				this.sandMate()
			},sandRed(){
			  	this.$axios({
			  	  method: "post",
			  	  url: "service.php?action=users_userInfo",
			  	  data: {},  
			  	}).then((res) => {
			  		let data=res.data;
			  
			  		if(data.code==1){
			  			this.info=data.data;
				
			  		}
			  	 
			  	});
			  
			  }, 
		sandMate(){
			
			this.$axios({
			  method: "get",
			  url: "stores/withdraw_list"
			}).then((res) => {
				let data=res.data;
		
				if(data.code==200){
							
				
					
					this.news=data.data.lists;
					
					
					
				}
			 
			});
		
		},sandAlt(){
				this.news=[]
				this.page=1
				this.sandMate()
			
			},  confirm(item){
			   	this.$axios({
			   	  method: "post",
			   	  url: "service.php?action=orders_qrshouhuo",
			   	  data: {orderId:item},  
			   	}).then((res) => {
			   		let data=res.data;
			   	
			   		if(data.code==1){
			   			
			   		Toast(data.message);
					
					 this.news=[]
			this.sandMate();
			   		}else{  
			   			Toast(data.message);
			   			      
			   		}
			   	 
			   	});
			   
			   },sandHans(){
			
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_channel",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
			
							data.data.forEach((item, index) => {
								if(this.channel_id==item.id){
									this.title=item.title
									this.hans=item.channels
									if(this.hans.length>0){
										this.channel_id=this.hans[0].id
									}
									this.sandMate();
									return
								}
							})			
					
						
					}
				 
				});
			
			}
		}
};
