<template>
  <div class="index">
    <Title titleName='技能培训' iconLeft="back" />
	
	<div class="news">
	
		<div class="cont">
						<div class="details">
							<div class="title">
								
								{{file.project}}
							</div>
							
							<div class="content">
						{{file.content}}
							</div>
						
						</div>
		</div>
		
	</div>



	
  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";

export default {
  components: {
    Title,
    CardList
  },
  data() {
    return {
		active: 1,
		id:'',
		file:''
    
    };
  },mounted() {
  	this.id = this.$route.query.id
	this.sandColl();

  },methods:{
	  sandColl(){
	  	
	  	this.$axios({
	  	  method: "post",
	  	  url: "/api/pxprodetail",
	  	  data: {pid:this.id},
	  	}).then((res) => {
	  		let data=res.data;
	  		
	  		if(data.code==200){
	  			
	  			this.file=data.data[0];
	  			
	  			
	  		
	  		}
	  	 
	  	});
	  },sandHall(){
	  	
	  	this.$axios({
	  	  method: "post",
	  	  url: "/api/addviews",
	  	  data: {id:this.id,type:2},
	  	}).then((res) => {});
	  },sand(name, title) {
     if(name==0){
		this.$router.replace({ path:"/news/list?id=0" });
	 }
	if(name==2){
			this.$router.replace({ path:"/news/list?id=2" });
	}
    }
  }
};
</script>
