<template>
  <div class="login">
   <Title titleName='申请入驻商家' iconLeft="back"/>
<img class="banner" src="../../assets/banner.png" />
	<div class="login_user">
		<div class="user_input">
			
		<div class="lab"><img src="../../assets/y1.png">店铺名称</div> <input v-model="reg.store_name" placeholder="请输入" />
		</div>
		<div class="user_input"  @click="showPicker = true">
			
			<div class="lab"><img src="../../assets/y2.png">城市  </div><input v-model="ying"  placeholder="请选择" /><div class="ip"><img src="../../assets/ip.png"></div>
			
		</div>
	<div class="user_input">
		
	<div class="lab"><img src="../../assets/y3.png">手机号</div> <input v-model="reg.phone" placeholder="请输入" />
	</div>
	
	
	<div class="user_input">
		
		<div class="lab"><img src="../../assets/y4.png">密码 </div><input type="password" v-model="reg.password"  placeholder="请输入" />
		
	</div>
	<div class="user_input">
		
		<div class="lab"><img src="../../assets/y4.png">确认密码 </div><input  type="password" v-model="reg.repassword"  placeholder="请输入" />
		
	</div>

	
		
	
	
		
	</div>
	<div class="h-txt">
		<textarea v-model="reg.cinemas" placeholder="请输入擅长的影院…"></textarea>
	</div>
	<div class="login_bt">
		<van-button @click="submitForm()" round color="#FF5E00" block>申请入驻</van-button>
	</div>
		

<van-popup v-model="show" position="bottom">
	<van-datetime-picker
	  @confirm="onConfirms"
	  type="date"
	  title="选择日期"
	  :min-date="minDate"
	/>
	
	</van-popup>
	<van-popup v-model="showPicker" position="bottom">
		<van-area :area-list="areaList" @confirm="onConfirm" :columns-num="2"  @cancel="showPicker = false"/>
		<!-- <van-picker show-toolbar  :columns="columns"  @cancel="showPicker = false"
		@confirm="onConfirm"/> -->
	<!-- <van-picker show-toolbar  :columns="columns"  @cancel="showPicker = false"
    @confirm="onConfirm"/> -->
</van-popup> 
<!-- 	<van-popup v-model="showPicker" position="bottom">
	  <van-picker
	    show-toolbar
	    :columns="news"
		value-key="DESCRIPTION"
	    @confirm="onConfirm"
	    @cancel="showPicker = false"
	  />
	</van-popup> -->
	
  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import { Toast } from 'vant';	
import AeraInfo from "../../common/area.js";
export default {
  components: {
    Title,
    CardList

  },
  data() {
    return {
		areaList: AeraInfo,
		columns: [
        {
          text: '浙江',
          children: [
            {
              text: '杭州',
              children: [{ text: '西湖区' }, { text: '余杭区' }],
            },
            {
              text: '温州',
              children: [{ text: '鹿城区' }, { text: '瓯海区' }],
            },
          ],
        },
        {
          text: '福建',
          children: [
            {
              text: '福州',
              children: [{ text: '鼓楼区' }, { text: '台江区' }],
            },
            {
              text: '厦门',
              children: [{ text: '思明区' }, { text: '海沧区' }],
            },
          ],
        },
      ],
		active: 1,
		id:'',
		news:'',
		file:'',
		ying:'',
		 showPicker:false,
		 show:false,
		 minDate: new Date(1900, 0, 1),
		reg: {
			store_name:'',
			city_code:'',
			phone:'',
			password:'',
			repassword:'',
			cinemas:''
			         
		      },
    
    };
  },mounted() {
	  
	if(this.$route.query.id){
		this.reg.id = this.$route.query.id
		this.sandCate()
	}
	  
  	
	this.sandMate()
	this.sandCase()
  },methods:{
	   onConfirm(value) {
		  
	    //    this.reg.areaName = value[0].name+'-'+value[1].name+'-'+value[2].name;
		   // this.reg.area_id=value[2].code
		   // this.reg.province_name=value[0].name
		//   this.reg.citys=value[1].name
		   // this.reg.area_name=value[2].name
		
		   this.reg.city_code=value[1].code
		   this.ying=value[1].name
	       this.showPicker = false;
	      },
	  submitForm() {
	  				
	  		
	          
	          this.$axios({
	            method: "post",
	            url: "stores/register",
	            data: this.reg,  
	          }).then((res) => {
	          	let data=res.data;
	          			
	          	if(data.code==200){
	  						
	  			  Toast(data.message);
				  this.$router.back()
	    
	          		
	          	}else{  
	  						  Toast(data.message);
	  						      
	  					}
	           
	          });
	  				 
	  				 
	         
	       
	       },sandCate(){
				this.$axios({
				  method: "post",
				  url: "service.php?action=userAddress_detail",
				  data: {id:this.reg.id},  
				}).then((res) => {
					let data=res.data;
				
					if(data.code==1){
						this.reg=data.data;
					}
				 
				});
			
			},cell(date) {
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();

      month = month < 10 ? "0" + month : month;
      day = day < 10 ? "0" + day : day;

      return year + "-" + month + "-" + day;
    },
	loginForm(){
				let that = this;
				let phone_num = this.loginData.phone;
				if(this.loginData.phone == ''){
				
					Toast('请输入手机号');
					return
				}else{
					if(!this.checkPhone(phone_num)){
                    return false;
                }
				}
				if(this.loginData.password == ''){
					Toast('请输入密码');
					return
				}
				let data = this.loginData;
				
				
				this.$axios({
				  method: "post",
				  url: "/wx/qylogin",
				  data:data,
				}).then((res) => {
			
					let data=res.data;
					if(data.code==200){
						Toast(data.message);
						localStorage.setItem("enterprise", JSON.stringify(data.data));
						this.$router.push({ path: "/loger" });	
						
					}else{
						Toast(data.message);
						}
				 
				});
				
				
				
				
				
				// loginFun(data).then(res =>{
				// 	console.log(res);
				// 	if(res.code == 0){
				// 		Toast('登录成功');
				// 		localStorage.setItem("userInfo", JSON.stringify(res.data.user));
				// 		that.$router.replace({ path: "/index" });
				// 	}else{
				// 		that.$toast(res.message);
				// 	}
				// })
			},checkPhone(phone){ 
				if(!(/^1[3456789]\d{9}$/.test(phone))){ 
					Toast('请填写正确的手机号');
					
					return false; 
				} else{
					return true
				}
			}
  }
};
</script>
