				border: none;
			}
		}
	.login_user .user_input{
		width:680px;
		color: #000000;
		font-size: 28px;
		height: 100px;
		border-bottom: 1px solid #eee;
		display: flex;
		margin-left: 20px;
		flex-direction: row;
		align-items: center;
		justify-content: flex-start;
		padding-right: 20px;
		.lab{
			width: 250px;
