import Vue from 'vue'
import VueRouter from 'vue-router'

import Index from '@/views/index/index'
import product from '@/views/index/product'
import products from '@/views/index/products'
import detail from '@/views/index/detail'
import details from '@/views/index/details'
import contact from '@/views/index/contact'
import order from '@/views/user/order'
import cart from '@/views/user/cart'
import ordDet from '@/views/user/details'
import orders from '@/views/user/orders'
import collect from '@/views/user/collect'
import pass from '@/views/user/pass'
import coupon from '@/views/user/coupon'
import inter from '@/views/user/inter'
import exc from '@/views/user/exc'
import sub from '@/views/user/sub'
import gift from '@/views/user/gift'
import info from '@/views/user/info'
import works from '@/views/index/works'
import det from '@/views/index/det'
import news from '@/views/index/news'
import rider from '@/views/product/rider'
import indPro from '@/views/product/index'
import address from '@/views/user/address'
import addAdr from '@/views/user/addAdr'
import register from '@/views/login/register'
import Users from '@/views/user/index'
import User from '@/views/user/user'
import List from '@/views/news/list'
import Details from '@/views/news/details'
import Act from '@/views/act/act'
import Acts from '@/views/act/index'

import DetailsNo from '@/views/news/detailsNo'

import business from '@/views/user/list'

import pxpro from '@/views/act/details.vue'


import kin from '@/views/login/kin'
import loger from '@/views/login/serve'
import Infos from '@/views/user/infos'

import comment from '@/views/user/comment'


import type from '@/views/index/type'
import ying from '@/views/index/ying'
import yue from '@/views/user/yue'
import ding from '@/views/index/ding'
Vue.use(VueRouter)

const router = new VueRouter({
    routes: [{
        path: '/',
        redirect: '/user/orders'
    }, {
        path: '/index',
        component: Index,
        children: [  {
                path: '/index/product',
                component: product,
            }, {
                path: '/index/products',
                component: products,
            },{
                path: '/index/detail',
                component: detail,
            },{
                path: '/index/details',
                component: details,
            },{
                path: '/index/works',
                component: works,
            },{
                path: '/index/det',
                component:det,
            },{
                path: '/index/news',
                component: news,
            },{
                path: '/index/contact',
                component: contact,
            },
           {
                path: '/index/act',
                component: Act,
            },  {
                path: '/index/type',
                component: type,
            },  {
                path: '/index/ying',
                component:ying,
            },  {
                path: '/index/ding',
                component:ding,
            }
        ]
    },{
        path: '/user',
        component: Users,
		redirect: '/user/user',
		children: [
			{
			    path: '/user/address',
			    component: address,
			},
			{
			    path: '/user/addAdr',
			    component: addAdr,
			},
			{
			    path: '/user/user',
			    component: User,
			},{
			    path: '/user/list',
			    component: business,
			},{
                path: '/user/order',
                component: order,
            },{
                path: '/user/cart',
                component: cart,
            },{
                path: '/user/orders',
                component: orders
            },{
                path: '/user/details',
                component: ordDet
            },{
                path: '/user/collect',
                component: collect
            },
			{
			    path: '/user/coupon',
			    component: coupon
			},{
			    path: '/user/pass',
			    component: pass
			},{
			    path: '/user/inter',
			    component: inter
			},{
			    path: '/user/exc',
			    component: exc
			},{
			    path: '/user/sub',
			    component: sub
			},{
			    path: '/user/gift',
			    component: gift
			},{
			    path: '/user/info',
			    component: info
			},{
			    path: '/user/comment',
			    component: comment
			},{
			    path: '/user/yue',
			    component:yue
			}
			
			
		]
    },{
        path: '/product',
        component: indPro,
		redirect: '/product/rider',
		children: [
			{
			    path: '/product/rider',
			    component: rider,
			}
			
		]
    },{
        path: '/act',
        component: Acts,
		redirect: '/act/act',
		children: [
			{
			    path: '/act/act',
			    component: Act,
			},{
			    path: '/act/details',
			    component: pxpro,
			},
			
		]
    },{
        path: '/register',
        component: register,
    },{
        path: '/kin',
        component: kin,
    },{
        path: '/loger',
        component: loger,
    },{
        path: '/infos',
        component: Infos
    }]
})



export default router