<template>
  <div class="login">
 <Title titleName='购物车' iconLeft="back"/>


	<div class="order">

<div class="pro-l2">


	<van-row class="can2" gutter='10' v-for="(item,index) in car_list">
		<van-col span="2">
			<img v-if="item.is_selected" src="../../assets/k2.png" @click="radio_change(item,index)" />
			<img v-else src="../../assets/k1.png" @click="radio_change(item,index)"/>
		</van-col>
		<van-col span="8"><img :src="item.image"></van-col>
		<van-col span="14">
			<p>{{item.title}}</p>
			<p><span>￥{{item.price_sale}}</span><span><van-stepper v-model="item.num" @change="valChange(item)" /></span></p>
		</van-col>
	</van-row>




</div>


	</div>

	<div class="adr">
	<div class="ad1"><img v-if="all_result"  src="../../assets/k2.png"  @click="all_fun" />
			<img v-else src="../../assets/k1.png"  @click="all_fun" />全选  合计：<b>￥{{price}}</b></div>
			<van-button color="#FF5E00"  @click="to_pay">提交订单</van-button>
	</div>

	
	
  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import { Toast } from 'vant';	
export default {
  components: {
    Title,
    CardList

  },
  data() {
    return {
		is_disabled:false,
		all_result: false,
		selected_list: [],
		selected_count: 0,
		price: 0,
		car_list: [],
		baoyou:'',
		weight:0
    
    };
  },mounted() {
	  this.sandCart();


  },methods:{
sandCart(){
				this.price=0;
				this.$axios({
				  method: "post",
				  url: "service.php?action=gouwuche_lists",
				  data: {},  
				}).then((res) => {
					let data=res.data;
					
					
					if(data.code==1){
						this.car_list=data.data;
						
						this.car_list.forEach((item, index) => {
						
						
						item['is_selected'] = false
						
						this.car_list[index] = item
							
						})
						
					}
				 
				});
			},to_pay() {
				if (this.selected_count === 0) {
					return false
				}
				let arr = []
				let product_info = []
				this.car_list.forEach((item, index) => {
					if (item.is_selected) {
						arr.push(item)
						product_info.push({
							"inventoryId": item.inventoryId,
							"productId": item.productId,
							"num": item.num
						})
					}
				})
				 localStorage.setItem("order_info", JSON.stringify(arr));
			 this.$router.push({ path: "/user/order?product_info="+ JSON.stringify(product_info) });	
				
			},
	all_fun() {
		if(this.car_list.length == 0){
			this.all_result = false;
			return false;
		}
		this.all_result = !this.all_result
		if (this.all_result) {
			this.selected_list = []
			this.selected_count = 0
			this.car_list.forEach((item, index) => {
				this.selected_count += 1
				item['is_selected'] = true
				this.car_list[index] = item
				this.selected_list.push(item)
				this.price = (Number(this.price) + Number(item.price_sale * item.num)).toFixed(2);
				this.weight = Number(this.weight) + Number(item.weight) * item.num
			})
			
		} else {
			this.selected_list = []
			this.selected_count = 0
			this.car_list.forEach((item, index) => {
				item['is_selected'] = false
				this.car_list[index] = item
				this.price = 0
				this.weight = Number(this.weight) - Number(item.weight) * item.num
			})
		}
		this.weight = this.weight.toFixed(2) 
	},
			radio_change(item, index) {
				item.price_sale = Number(item.price_sale)
				this.car_list[index]['is_selected'] = !item.is_selected
				if (this.car_list[index]['is_selected']) {
					this.selected_count += 1
					this.price = (Number(this.price) + Number(item.price_sale * item.num)).toFixed(2)
					this.all_result = this.car_list.length != 0 && this.selected_count == this.car_list.length ? true : false
					this.weight = Number(this.weight) + Number(item.weight)* item.num
				} else {
					this.selected_count -= 1
				
					this.price = (Number(this.price) - Number(item.price_sale * item.num)).toFixed(2)
					this.weight = Number(this.weight) - Number(item.weight)* item.num
					this.all_result = false
					this.weight = this.weight.toFixed(2)
				}
			},	valChange(val) {
			
			this.price =0
				this.car_list.forEach((item, index) => {
					if(item['is_selected']){
						this.price += Number(item.price_sale * item.num)
					}
				})
				this.handleChange(val.num,val.inventoryId)
				
				
				
			},handleChange(num,inventoryId){
			this.$axios({
			  method: "post",
			  url: "service.php?action=gouwuche_updateNum",
			  data: {inventoryId:inventoryId,num:num},  
			}).then((res) => {
				let data=res.data;
			
				if(data.code==1){
					
					this.sandCart()
				}
			 
			});
		},
		
			
			onConfirm(value) {
     this.pay_text=value.text
	 this.pay_type =value.value
	 this.showPicker=false
    },	get_money(val1, val2, val3) {
				if(this.is_use_jifen){
					val2 = val2 || 0
					if (Number(val1) > (Number(val2)+Number(val3))) {
						return (Number(val1) - Number(val2)-Number(val3)-Number(this.info.yunfei)).toFixed(2)
					}
					return 0;
				}else{
					val2 = val2 || 0
					if (Number(val1) > Number(val2)) {
						return (Number(val1) - Number(val2)-Number(this.info.yunfei)).toFixed(2)
					}
					return 0;
				}
				
			},checkPhone(phone){ 
				if(!(/^1[3456789]\d{9}$/.test(phone))){ 
					Toast('请填写正确的手机号');
					
					return false; 
				} else{
					return true
				}
			}, 	user_coupon(item) {
				this.coupon_info = item
				this.params_info.yhq_id = item.id
				this.is_show_coupon = false
			}, sandCase(){
			  	this.$axios({
			  	  method: "post",
			  	  url: "service.php?action=orders_getPayInfo",
			  	  data: {product_info:JSON.stringify(this.product_info)},  
			  	}).then((res) => {
			  		let data=res.data;
			  
			  		if(data.code==1){
			  					
			  			this.info=data.data;
						
			  		
			  		}
			  	 
			  	});
			  
			  },	use_jifen() {
			
				this.is_use_jifen = !this.is_use_jifen
			},	  sandRed(id){
			  	this.$axios({
			  	  method: "post",
			  	  url: "service.php?action=paypal_pay",
			  	  data: {order_id:id},  
			  	}).then((res) => {
			  		let data=res.data;
			  
			  		if(data.code==1){
			  			window.location.href=data.data.link	
				
			  		}
			  	 
			  	});
			  
			  },
  }
};
</script>
<style lang="scss" scoped>
.order{
	font-size: 28px;
	padding-bottom: 200px;
}
.pro-l2{
	
	
	.can{
		
		border-bottom: 1px solid #DDDDDD;
		display: flex;
		justify-content: space-between;
		height: 100px;
		align-items: center;
		.pl{
			display: flex;
			align-items: center;
			img{
				width: 30px;
				height: 30px;
			}
			
		}
	}
	.can1{
		height: 100px;
		line-height: 100px;
		border-bottom: 1px solid #DDDDDD;
	}
	.can2{
		background: #FFFFFF;
		display: flex;
		align-items: center;
		margin-top: 20px;
		padding: 20px;
		img{
			width: 100%;
		}
		p{
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom:15px;
		}
	}
	.coupon{
		li{
			padding: 20px;
			border-bottom: 1px solid #DDDDDD;
			display: flex;
			justify-content: space-between;
		}
	}

}
	.can4{
		margin-top: 20px;
		padding: 0 20px;
		background: #FFFFFF;
		border-bottom: 1px solid #DDDDDD;
		display: flex;
		justify-content: space-between;
		height: 100px;
		align-items: center;
		.pl{
			display: flex;
			align-items: center;
			img{
				width: 30px;
				height: 30px;
			}
			input{
				border: none;
				text-align: right;
				width: 600px;
			}
		}
	}
 .pro-tit{
	 background: #FFFFFF;
	 text-align: center;
	 line-height: 40px;
	 padding: 20px 0;
	 h3{
		 font-size: 36px;
	 }
	 p{
		 font-size: 24px;
	 }
 }
 .adr{
 	display: flex;
 	align-items: center;
	justify-content: space-between;
 	position: fixed;
 	bottom: 0;
 	width: 680px;
 	height: 68px;
 	background: #FFFFFF;
 	font-size: 28px;
 	box-shadow: 0px -4px 10px rgba(0, 0, 0, 0.06);
	padding: 20px;
	.ad1{
		display: flex;
		align-items: center;
		img{
			width:40px;
			margin-right: 20px;
		}
	}
	b{
		font-size: 36px;
		color: #E42626;
	}
 }
</style>