<template>
  <div class="login">
    <Title />

	<div class="pro-tit">
		<h3>帳戶安全</h3>
		<p>ACCOUNT SECURITY</p>
	</div>
	<div class="mem-con">
	
	<div class="mem-l1">
		<van-circle v-model="currentRate"  layer-color="#F4F4F4" color="#000" size="150px" :rate="user.safe_number" :speed="100"  >
			<slot slot="default">
				<h1>{{user.safe_number}}<span>%</span></h1>
				<p>帳戶安全等級</p>
			</slot>
		</van-circle>
	</div>
	<div class="mem-l2">
		<ul>
			<li>
				<van-row gutter="20">
					<van-col span='20'>
						<h6>登入帳戶</h6>
						<p>它可以用來登入。它是您帳戶的唯一標識</p>
					</van-col>
					<van-col span='4'><van-button type="default" size="small" to="/user/info">修改</van-button></van-col>
				</van-row>
			</li>
			<li>
				<van-row gutter="20">
					<van-col span='20'>
						<h6>登入密碼</h6>
						<p>為確保您的購物安全，建議您定期更改密碼
以保護您的帳戶安全</p>
					</van-col>
					<van-col span='4'><van-button type="default" size="small" to="/user/pass">修改</van-button></van-col>
				</van-row>
			</li>
			<li>
							<van-row gutter="20">
								<van-col span='20'>
									<h6>綁定手機</h6>
									<p>檢索密碼和驗證身份</p>
								</van-col>
								<van-col span='4'><van-button color="#000000" size="small" to="/user/info">验证</van-button></van-col>
							</van-row>
						</li>
						<li>
							<van-row gutter="20">
								<van-col span='20'>
									<h6>驗證郵箱</h6>
									<p>您的郵箱:  {{user.username}}</p>
								</van-col>
								<van-col span='4'><van-button type="default" size="small" to="/user/info">修改</van-button></van-col>
							</van-row>
						</li>
		</ul>
	</div>
	</div>
	
	
	
	
	
	  <Bar />
  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import Bar from '@/components/bar/bar'
import { Toast } from 'vant';	
export default {
  components: {
    Title,
    Bar

  },
  data() {
    return {
		currentRate: 0,
		user:'',
		active: 1,
		id:'',
		file:'',
		ruleForm:{
			email:'',
			password:''
		}
    
    };
  },mounted() {
  		this.sandMate();
this.sandMate();
  },methods:{
	sandMate(){
		this.$axios({
		  method: "post",
		  url: "service.php?action=users_userinfo",
		  data: {},  
		}).then((res) => {
			let data=res.data;
	
			if(data.code==1){
		
								
				this.user=data.data;
							localStorage.setItem("user", JSON.stringify(data.data));
				
			}
		 
		});
	
	},loginForm(){
				
				if(this.ruleForm.phone == ''){
				
					Toast('请输入邮箱');
					return
				}
				if(this.ruleForm.password == ''){
					Toast('请输入密码');
					return
				}
				
				
			this.$axios({
			      method: "post",
			      url: "service.php?action=users_emailLogin",
			      data: this.ruleForm,  
			    }).then((res) => {
			    	let data=res.data;
			    			
			    	if(data.code==1){
									
						localStorage.setItem("user", JSON.stringify(data.data));
						Toast(data.message);
					this.$router.push({ path: "/" });	
			    		
			    	}else{  
									Toast(data.message);
									      
								}
			     
			    });
				
				
				
				
			
			},checkPhone(phone){ 
				if(!(/^1[3456789]\d{9}$/.test(phone))){ 
					Toast('请填写正确的手机号');
					
					return false; 
				} else{
					return true
				}
			}
  }
};
</script>
