<template>
  <div class="act">
     <Title titleName='技能培训' iconLeft="back"/>


		<div class="news">
		{{file}}
			<div class="cont2">
				<ul>
					
					<li v-for="item in files">
						<router-link :to="'/act/details?id='+item.id">
						<img src="../../assets/case1.png" />
						<div class="txt">
							{{item.project}}
						</div>
						</router-link>
					</li>
					
				
				</ul>
			</div>
			
		</div>
  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";

export default {
  components: {
    Title,
    CardList
  },
  data() {
    return {
		active: 1,
		id:'',
		file:'',
		files:''
    
    };
  },mounted() {
  
	this.sandColl();

  },methods:{
	  sandColl(){
	  	
	  	this.$axios({
	  	  method: "post",
	  	  url: "/api/pxindex",
	  	  data: {},
	  	}).then((res) => {
	  		let data=res.data;
	  		
	  		if(data.code==200){
	  			
	  			this.file=data.data.content;
	  			
	  			
	  		
	  		}
	  	 
	  	});
		
		
		this.$axios({
		  method: "post",
		  url: "/api/pxprolist",
		  data: {},
		}).then((res) => {
			let data=res.data;
			
			if(data.code==200){
				
				this.files=data.data;
				
				
			
			}
		 
		});
	  }
  }
};
</script>
<style lang="scss">
.act{
	.news{
		.cont2{
			
			li{
				width: 47%;
			}
		}
	}
	
}
</style>