<template>
  <div class="index">
  <Title titleName='产品' iconLeft="back"/>
  
<img class="heath" v-if="ad==1" src="../../assets/news1.png" />
<van-dropdown-menu  active-color="#FF5E00">
  
 <van-dropdown-item title="品牌" ref="item1">
	 <div class="han-price">
		
		 <ul class="pin">
		 	<li @click="brandId=item.id" :class="{'give':brandId==item.id}" v-for="item in mile">{{item.brand}}</li>
		 	
		 </ul>
		 <div class="buts">
			 <div class="lig pu" @click="onPain1">重置</div> <div class="lig" @click="onConfirm1">确定</div>
		 </div>
	 </div>
	
 </van-dropdown-item>
 <van-dropdown-item title="功效" ref="item2">
 	 <div class="han-price">
 		
 		 <ul class="pin">
 		 	<li @click="fun=item" :class="{'give':fun==item}" v-for="item in funs">{{item}}</li>
 		 	
 		 </ul>
 		 <div class="buts">
 			 <div class="lig pu" @click="onPain2">重置</div> <div class="lig" @click="onConfirm2">确定</div>
 		 </div>
 	 </div>
 	
 </van-dropdown-item>
 <van-dropdown-item title="价格" ref="item">
	 <div class="han-price">
		 <ul>
		 	<li @click="pqj=item" :class="{'give':pqj==item}" v-for="item in pud">{{item}}</li>
		 	
		 </ul>
		 <div class="buts">
			 <div class="lig pu" @click="onPain">重置</div> <div class="lig" @click="onConfirm">确定</div>
		 </div>
	 </div>
	
 </van-dropdown-item>
</van-dropdown-menu>
	<div class="home">
	
	
	
	
		
		<div class="index-product">
		
			<div class="con">
				<ul>
					<li v-for="it in news">
						 <router-link :to="'/index/detail?id='+it.inventoryId">
							
						<div class="tu">
							<img :src="it.img" />
						</div>
						<div class="txt">
							<div class="bot">{{it.title}}</div>
							<p class="money">￥{{it.price_sale}}</p>
							<p class="has">￥{{it.price_market}}</p>
						</div>
						 </router-link>	
					</li>
				
				</ul>
			<div class="more">
				
				<div class="button" @click="more">{{tip}}</div>
				
			</div>
			</div>
			
		</div>
		
	</div>

	



  </div>
</template>
<script>
import Title from "@/common/title";
import CardList from "@/components/cardList/cardList";
import noodles from "@/assets/noodles.png";
import Bar from '@/components/bar/bar'
export default {
  components: {
     Title,
    CardList,
	Bar
  },
  data() {
    return {
		images:[],
		info:'',
		page:1,
		channel_id:0,
		news:[],
		notice:[],
		funs:[],
		name:'',
		hans:'',
		sfznum:'',
		phone:'',
		tip:'',
		iscf:'',
		con:'',
		brandId:'',
		mile:[],
		fun:'',
		pqj:'',
		ad:'',
		value: 0,
		switch1: false,
		switch2: false,
		pud:[],
		option: [
		  { text: '全部商品', value: 0 },
		  { text: '新款商品', value: 1 },
		  { text: '活动商品', value: 2 },
		]

    };
  },  mounted() {
	this.ad = this.$route.query.ad
	this.brandId = this.$route.query.pd
	this.channel_id = this.$route.query.id
	this.con = this.$route.query.con
	this.name = window.localStorage.getItem('name')
	this.sfznum = window.localStorage.getItem('sfznum')
	this.phone = window.localStorage.getItem('phone')
	
	this.sandAlt();
	this.sandPid();
	this.full()
	this.sandHans();
		
		this.sandCase();
		},methods: {
			 onConfirm() {
			   this.$refs.item.toggle();
			   this.news=[]
			   this.sandMate()
			 }, onPain() {
			   this.$refs.item.toggle();
			   this.pqj=''
			 } ,onConfirm1() {
			   this.$refs.item1.toggle();
			   this.news=[]
			   this.sandMate()
			 }, onPain1() {
			   this.$refs.item1.toggle();
			   this.brandId=''
			 } ,onConfirm2() {
			   this.$refs.item2.toggle();
			   this.news=[]
			   this.sandMate()
			 }, onPain2() {
			   this.$refs.item2.toggle();
			   this.fun=''
			 },onSearch(val) {
			      Toast(val);
			    },	more(){
				this.page+=1;
				this.sandMate()
			},
			full(){
				if(this.ad=='1'){
					this.channel_id=6
					this.iscf=1
				}
				if(this.ad=='2'){
					this.channel_id=7
			
				}
				if(this.ad=='3'){
					this.channel_id=6
				
				}
				this.sandMate();
				this.sandPay();
				this.sandGrade()
			},sandPid(){
			
				
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_pqjlist",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
				
									
						this.pud=data.data;
						
						
				
						
					}
				 
				});
			
			},
		sandMate(){
			this.$axios({
			  method: "post",
			  url: "service.php?action=product_plist",
			  data: {page:this.page,channel_id:this.channel_id,pqj:this.pqj,brandId:this.brandId,fun:this.fun,iscf:this.iscf,keyword:this.con},  
			}).then((res) => {
				let data=res.data;
					
				if(data.code==1){
					
					
				
				if(data.data.length==0&&this.page==1){
					this.tip='暂无内容'
					return
				}
				
				if(data.data.length==0&&this.page!=1){
					this.tip='没有更多了'
					return
				}
				this.tip='查看更多'
				this.news=this.news.concat(data.data);
				
					
					
				}
			 
			});
		
		},sandGrade(){
			
				
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_funlist",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
				
									
						this.funs=data.data;
						
						
				
						
					}
				 
				});
			
			},sandCase(){
				this.$axios({
				  method: "post",
				  url: "/api/noticelist",
				  data: {page:1},  
				}).then((res) => {
					let data=res.data;
					
					if(data.code==200){
						
						this.notice=data.data[0];
						
						
					
					}
				 
				});
			},sandAlt(){
				this.$axios({
				  method: "post",
				  url: "service.php?action=index_index",
				  data: {type:2},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
				
										
						this.images=data.data.banners;
						
						
					}
				 
				});
			
			},sandHans(){
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_channel",
				  data: {},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
				
										
						this.hans=data.data;
						
						
					}
				 
				});
			
			},sandPay(){
			
				
				this.$axios({
				  method: "post",
				  url: "service.php?action=product_brandlist",
				  data: {channelId:this.channel_id},  
				}).then((res) => {
					let data=res.data;
			
					if(data.code==1){
				
									
						this.mile=data.data;
						
						
				
						
					}
				 
				});
			
			}
		}
};
</script>
